import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css';
import lsdIcons from '../../../../utils/lsdIcons';
import LicensesToggles from "../LicensesToggles/LicensesToggles";
import Tooltip from "../../../Tooltip/Tooltip";



export default class AddNewResource extends Component {

    constructor() {
        super();

        let point4 = labels.OBNewResourcePoint4.split('$0');

        this.tooltipText = (<div>{labels.OBNewResourcePoint1}
                <ul className="rd-comments-ul">
                    <li>{labels.OBNewResourcePoint2}</li>
                    <li>{labels.OBNewResourcePoint3}</li>
                    <li>{point4[0]}<br/>{point4[1]}</li>
                </ul>
            </div>);

        this.state = {
            saving: false,
            user: null,
            resourceName: null,
            territory: null,
            ganttLabel: null,
            territories: [],
            licenses: {
                fsl: true,
                mobile: false
            },
        };

        this.inputs = {}
    }

    render() {

        let savingOrButtonElemet = this.state.saving ? (<div className="rd-add-saving-new-res"><img src={lsdIcons.spinner} /> {labels.OnboardingSaving}</div>) : (<button className="guided-setup-button guided-setup-button_neutral" onClick={this.addNewResource.bind(this)}>{labels.NewWorkTypeButton}</button>);


        return (
            <div id="rd-add-new-resource">
                <h1>
                    {labels.NewREsources} <Tooltip text={this.tooltipText} tipPosition="right" />
                </h1>

                <div className="rd-new-resource-cell">
                    { this.generateUserOptions() }
                </div>

                <div className="rd-new-resource-cell">
                    <input type="text" value={this.state.resourceName} placeholder={labels.ObResourceName} ref={i => this.inputs['resourceName'] = i } onChange={this.valueChanged.bind(this, 'resourceName')} />
                </div>

                <div className="rd-new-resource-cell">
                    <input type="text" value={this.state.ganttLabel} placeholder={labels.ObGanttLabel} ref={i => this.inputs['ganttLabel'] = i }onChange={this.valueChanged.bind(this, 'ganttLabel')} />
                </div>

                <div className="rd-new-resource-cell">
                    { this.generateTerritoriesOptions() }
                </div>

                <div className="rd-new-resource-cell rd-new-resource-cell-small">
                    <LicensesToggles fsl={this.state.licenses.fsl} mobile={this.state.licenses.mobile} onChange={this.licenseChanged.bind(this)} />
                </div>

                <div className="rd-new-resource-cell rd-new-resource-cell-small">
                    {savingOrButtonElemet}
                </div>




            </div>
        );


    }


    generateUserOptions() {

        let options = [<option value=''>{labels.ObSelectUser}</option>];

        this.props.users.forEach(user => {

            if ( user.IsActive && (!this.props.resources[user.Id])) {
                options.push(<option value={user.Id}>{user.Name}</option>);
            }
        });

        return (
            <select onChange={this.userChanged.bind(this)} value={this.state.user} ref={i => this.inputs['user'] = i }>
                {options}
            </select>
        );

    }


    generateTerritoriesOptions() {

        let options = [<option value=''>{labels.ObSelectTerritory}</option>];

        if (this.props.territories.length === 0) {
            return (<select onChange={this.valueChanged.bind(this, 'territory')} value={this.state.territory}>{options}</select>);
        }

        this.props.territories
            .filter(territory => territory.IsActive)
            .forEach(territory => options.push(<option value={territory.Id}>{territory.Name}</option>));

        return (
            <select onChange={this.valueChanged.bind(this, 'territory')} value={this.state.territory} ref={i => this.inputs['territory'] = i }>
                {options}
            </select>
        );
    }


    userChanged(e) {

        if (!this.state.resourceName && e.target.value !== "") {
            this.setState({
                user: e.target.value,
                resourceName: this.props.users.find(user => user.Id === e.target.value).Name
            });
        } else {
            this.setState({ user: e.target.value });
        }

    }


    licenseChanged(type, value, cb) {

        let licenses = Object.assign({}, this.state.licenses);

        licenses[type] = value;

        this.setState({licenses});

        cb();

    }


    valueChanged(key, e) {
        let change = {};
        change[key] = e.target.value;
        this.setState(change);
    }

    addNewResource() {

        if (this.state.saving) {
            return;
        }

        if (!this.state.resourceName) {
            alert(labels.MustEnterResourceName);
            return;
        }

        if (!this.state.user) {
            alert(labels.MustEnterUser);
            return;
        }

        if (!this.state.territory) {
            alert(labels.MustSelectServiceTerritory);
            return;
        }

        if (!this.state.licenses.fsl && !this.state.licenses.mobile) {
            window.alert(labels.ResourceMustHaveLicenses);
            return;
        }


        let resource = {
            resourceName: this.state.resourceName,
            user: this.state.user,
            territory: this.state.territory,
            ganttLabel: this.state.ganttLabel,
        };

        this.setState({saving: true});

        this.props.onAdded(resource, this.state.licenses, (gotErrors = false) => {

            if (!gotErrors) {

                this.setState({
                    saving: false,
                    user: null,
                    resourceName: null,
                    territory: null,
                    ganttLabel: null,
                    licenses: {
                        fsl: true,
                        mobile: false
                    }
                });

                for (let k in this.inputs) {
                    this.inputs[k].value = '';
                }

            } else {
                this.setState({saving: false});
            }

        });


    }

}