import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import lsdIcons from '../../../../utils/lsdIcons';
import './styles.css';

export default class LicensesToggles extends Component {

    constructor(props) {
        super(props);

        this.state = {
            saving: {
                mobile: false,
                fsl: false
            }
        }
    }


    render() {



        return(
            <div>

                {this.generateFslLicense()}
                {this.generateMobileLicense()}

            </div>
        );

    }


    generateFslLicense() {

        let fslClasses = `slds-icon licenseIcon ${this.props.fsl ? 'fslLicense' : ''}`;

        if (this.state.saving.fsl) {
            return <img src={lsdIcons.spinner} className="lt-saving-icon lt-saving-fsl" />;
        }

        return (
            <span title={this.props.fsl ? labels.RemoveFslLicenseAlt : labels.AddFslLicenseAlt}>
                <svg aria-hidden="true"
                     onClick={this.licenseChanged.bind(this,'fsl', !this.props.fsl)}
                     className={fslClasses}>
                        <use href={lsdIcons.fslLicense} />
                </svg>
            </span>
        )
    }


    generateMobileLicense() {

        let mobileClasses = `slds-icon licenseIcon fslMobile ${this.props.mobile ? 'mobileLicenseOn' : ''}`;

        if (this.state.saving.mobile) {
            return <img src={lsdIcons.spinner} className="lt-saving-icon" />;
        }

        return (
            <span title={this.props.mobile ? labels.RemoveMobileLicenseAlt : labels.AddMobileLicenseAlt}>
                <svg aria-hidden="true"
                     onClick={this.licenseChanged.bind(this,'mobile', !this.props.mobile)}
                     className={mobileClasses}>
                        <use href={lsdIcons.mobileLicense} />
                </svg>
            </span>
        )
    }


    licenseChanged(type, value) {

        if (this.state.saving[type]) {
            return;
        }

        let state = Object.assign({}, this.state);
        state.saving[type] = true;
        this.setState(state);

        this.props.onChange(type, value, () => {

            let state = Object.assign({}, this.state);
            state.saving[type] = false;
            this.setState(state);

        })

    }

}
