import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import debounce from 'lodash/debounce';
import './styles.css';
import lsdIcons from '../../../../utils/lsdIcons';



export default class SearchResources extends Component {

    constructor(props) {
        super(props);
        this.searchTermChangedDebounced = debounce(props.onChange, 100);
    }


    render() {

        return (
            <div id="rd-search-bar">
                <input type="text" placeholder={labels.OBSearchResources} onChange={this.searchTermChanged.bind(this)} />
                <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.search} /></svg>
            </div>
        );

    }

    searchTermChanged(e) {
        this.searchTermChangedDebounced(e.target.value);
    }
}