import React, { Component } from 'react';
import lsdIcons from '../../utils/lsdIcons';
import './styles.css';


export default class Tooltip extends Component {

    render() {

    	let fslClasses = 'slds-icon tooltip-icon';

        let tooltipPositionClass = 'rules-select-help-tooltip';
        if (this.props.tipPosition) {
            if (this.props.tipPosition == 'up')
                tooltipPositionClass = 'rules-select-help-tooltip tooltip-up';
            else if (this.props.tipPosition == 'right')
                tooltipPositionClass = 'rules-select-help-tooltip tooltip-right';
        }
        else {
            tooltipPositionClass = 'rules-select-help-tooltip tooltip-down';
        }

        return (
            <span className="is-relative">
                <span className="rules-select-help">
                	<svg aria-hidden="true"
                	     className={fslClasses}>
                	        <use href={lsdIcons.info} />
                	</svg>
                </span>
                <span className={tooltipPositionClass}>{this.props.text}</span>
            </span>
        );

    }

}


