import React, { Component } from 'react';
import _ from 'lodash';
import labels from '../../../../utils/customLabels';
import NewWorkType from './NewWorkType/NewWorkType';
import WorkTypeItem from './WorkTypeItem/WorkTypeItem';
import SearchInput from '../../../SearchInput/SearchInput';
import './styles.css';


class WorkTypesList extends Component {

    constructor() {
        super();

        this.state = {
            searchTerm: '',
            selectedId: null
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.workTypes) {
            this.allWorkTypes = nextProps.workTypes;
        }
    }

    __renderWorkTypeItems() {

        let filteredWorkTypes = this.allWorkTypes;

        if (this.state.searchTerm) {
            filteredWorkTypes = _.filter(this.allWorkTypes, wt => wt.Name.toUpperCase().indexOf(this.state.searchTerm.toUpperCase()) > -1);
        }

        return _.map(filteredWorkTypes, (wt, index) =>
            <WorkTypeItem key={index}
                          worktype={wt}
                          onSelectedWorkType={this.__setSelectedWorkType.bind(this)}
                          isSelected={this.state.selectedId == wt.Id} />);
    }

    __setSelectedWorkType(worktype) {
        this.setState( {selectedId: worktype.Id} );
        this.props.selectWorkType(worktype);
    }

    __addNewWorkType(workType) {
        this.allWorkTypes.push(workType);
        this.__setSelectedWorkType(workType);
        this.props.afterSave([workType]);
    }

    __validateNewWorkType(name) {


        if (!name.replace(/\s/g, '').length) {
            alert(labels.WorkTypeNameCantBeBlank);
            return false;
        }

        // check if name already exist
        for (let i=0; i<this.allWorkTypes.length; i++) {
            if (this.allWorkTypes[i].Name.toLowerCase() === name.toLowerCase()) {
                alert(labels.TerritoryNameExist);
                return false;
            }
        }

        return true;
    }

    __setSearchTerm(searchTerm) {
        this.setState( {searchTerm} );
    }

    render() {

        return (

            <div className="worktypes-container">
                <NewWorkType onNewAdded={this.__addNewWorkType.bind(this)}
                             validate={this.__validateNewWorkType.bind(this)}/>
                <SearchInput doSearch={this.__setSearchTerm.bind(this)} />
                <div className="worktype-list">
                    {this.__renderWorkTypeItems()}
                </div>
            </div>

        );
    }

}

export default WorkTypesList;
