import React, { Component } from 'react';
import './styles.css';
import lsdIcons from '../../utils/lsdIcons';

class Lightbox extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {

        if (!this.props.show) {
            return null;
        }

        let styles = {
                'height': this.props.height,
                'max-height': this.props.maxHeight,
                'width': this.props.width
            },

            closeContainerStyle = {
                width: Number.parseInt(this.props.width) + 30
            };



        return (

            <div className="lightbox-black">

                <div className="lightbox-close-container" style={closeContainerStyle}>
                    <svg aria-hidden="true" className="slds-icon lightbox-close-icon" onClick={this.__handleVisibility.bind(this)}>
                        <use href={lsdIcons.close}></use>
                    </svg>
                </div>


                <div
                    className="lightbox-container"
                    style={styles}>
                    {this.props.children}
                </div>

            </div>
        );

    }


    __handleVisibility() {
        this.props.handleVisibility();
    }

}


export default Lightbox;