import React, { Component } from 'react';
import './styles.css';
import Lightbox from "../../../Lightbox/Lightbox";
import RulesExplanationLightbox from "./RulesExplanationLightbox";
import ObjectNames from '../../../../utils/ObjectNames';
import {mandatoryRules} from '../../../../utils/miscSalesforceObjects';
import lsdIcons from '../../../../utils/lsdIcons';
import Tooltip from "../../../Tooltip/Tooltip";
import labels from '../../../../utils/customLabels';


export default class RulesSelector extends Component {

    constructor(props) {

        super(props);

        this.inputs = {};

        let selectedRules = this.__filterSelectedRules(props.policyId, props.rules, props.rulesJunction, true),
            unselectedRules = this.__filterSelectedRules(props.policyId, props.rules, props.rulesJunction, false);

        this.state = {
            showRulesExplanationLightbox: false,
            saving: false,
            selectedRules,
            unselectedRules
        };

        this.tooltipText = (<div>
                <div>{labels.RulesPreselectedTooltip}</div><br/>
                <div>{labels.RulesPreselectedTooltip2}</div><br/>
                <div>{labels.RulesPreselectedTooltip3}</div>
            </div>);

    }



    render() {

        let availableActions = null;

        if (!this.state.saving) {
            availableActions = (
                <div className="rules-buttons-container">
                    <div className="rules-move-button" onClick={this.__addRules.bind(this)}>&gt;</div>
                    <div className="rules-move-button" onClick={this.__removeRules.bind(this)}>&lt;</div>
                </div>
            )
        } else {
            availableActions = (<div className="rules-buttons-container"><img src={lsdIcons.spinner} /></div>);
        }

        return (

            <div>

                <h2 className="rules-h2">{labels.RulesSelection}</h2>
                <p className="rules-p">
                    {labels.RulesExplainOb} <br/> {labels.RulesExplainObCont} <b className="label-link" onClick={this.__toggleRulesExplanationLightbox.bind(this)}>{labels.RulesExplainMore}</b> <br/>{labels.RulesExplainObCont2}
                </p>


                <Lightbox show={this.state.showRulesExplanationLightbox} height="420px" width="650px" handleVisibility={this.__toggleRulesExplanationLightbox.bind(this)}>
                    <RulesExplanationLightbox />
                </Lightbox>

                <div id="RulesSelectionContainer">
                    <div className="rules-select-container">

                        <div className="rules-select-header">{labels.AvailableRules}</div>

                        <select multiple
                                className="rules-selector-select"
                                ref={ input => { this.inputs.unselectedRulesSelect = input }}>
                            {this.__generateRulesOptions(this.state.unselectedRules)}
                        </select>
                    </div>

                    {availableActions}

                    <div className="rules-select-container">

                        <div className="rules-select-header">
                            {labels.SelectedRules}

                            <Tooltip text={this.tooltipText} tipPosition='up' />

                        </div>

                        <select multiple
                                className="rules-selector-select"
                                ref={ input => { this.inputs.selectedRulesSelect = input }}>
                            {this.__generateRulesOptions(this.state.selectedRules, true)}
                        </select>

                        {!this.__doesHaveAvailabilityRule() ? (<div className="ob-noars">{labels.NoAvabRule}</div>) : null}


                    </div>
                </div>

            </div>

        )

    }


    componentWillReceiveProps(nextProps) {


        if (this.props !== nextProps.policyId && nextProps.policyId && nextProps.rules && nextProps.rulesJunction) {

            let policyId = nextProps.policyId,
                rules = nextProps.rules,
                rulesJunction = nextProps.rulesJunction;

            let selectedRules = this.__filterSelectedRules(policyId, rules, rulesJunction, true),
                unselectedRules = this.__filterSelectedRules(policyId, rules, rulesJunction, false);

            this.setState({selectedRules, unselectedRules});
        }

    }


    __addRules() {

        if (this.inputs.unselectedRulesSelect.selectedOptions.length > 0 && !this.state.saving) {

            let selectedRules = [],
                select = this.inputs.unselectedRulesSelect.selectedOptions;

            for (let i=0; i<select.length; i++) {

                let junction = { Id: null };
                junction[ObjectNames.SchedulingPolicyWorkRule.SchedulingPolicy] = this.props.policyId;
                junction[ObjectNames.SchedulingPolicyWorkRule.WorkRule] = select[i].value;

                selectedRules.push(junction);
            }

            this.setState({saving: true});

            this.props.onAdded(selectedRules, 'add', () => {
                let elements = this.inputs.unselectedRulesSelect.options;

                for(let i = 0; i < elements.length; i++){
                    elements[i].selected = false;
                }

                this.setState({saving: false});
            });
        }
    }


    __removeRules() {

        if (this.inputs.selectedRulesSelect.selectedOptions.length > 0 && !this.state.saving) {

            let selectedRules = [],
                select = this.inputs.selectedRulesSelect.selectedOptions;

            for (let i=0; i<select.length; i++) {
                selectedRules.push(select[i].value);
            }

            this.setState({saving: true});

            this.props.onRemoved(selectedRules, 'remove', () => {

                let elements = this.inputs.selectedRulesSelect.options;

                for(let i = 0; i < elements.length; i++){
                    elements[i].selected = false;
                }

                this.setState({saving: false});
            });
        }

    }



    // show only relevant rules (applied to the policy and not in the policy)
    __filterSelectedRules(policyId, rules, junctions, bringSelected) {

            return rules.filter(rule => {
                if (bringSelected) {
                    return !!junctions.find( j => j.SchedulingPolicy === policyId && j.WorkRule === rule.Id);
                } else {
                    return !junctions.find( j => j.SchedulingPolicy === policyId && j.WorkRule === rule.Id);
                }

        })

    }


    // generate the option tags for the rules select element
    __generateRulesOptions(rules, isSelected = false) {
        return rules.sort( (r1,r2) => r1.Name <= r2.Name ? -1:1 ).map(rule => {

            if (isSelected && (rule.Id === mandatoryRules.earlyStart || rule.Id === mandatoryRules.dueDate)) {
                return (
                    <option value={rule.Id} disabled title={rule.RecordType.Name.substr(16,rule.RecordType.Name.length)}>{rule.Name}</option>
                )
            }

            return (
                <option value={rule.Id} title={rule.RecordType.Name.substr(16,rule.RecordType.Name.length)}>{rule.Name}</option>
            )
        });
    }


    // open/close the lightbox
    __toggleRulesExplanationLightbox() {
        this.setState({showRulesExplanationLightbox : !this.state.showRulesExplanationLightbox});
    }


    // availability rule not available
    __doesHaveAvailabilityRule() {

        let got = false;

        this.state.selectedRules.forEach(rule => {
            if (rule.RecordType.DeveloperName === 'Gap_Rule_Service') {
                got = true;
            }
        });

        return got;

    }

}