import React, { Component } from 'react';
import debounce from 'lodash/debounce';
import lsdIcons from '../../utils/lsdIcons';
import labels from '../../utils/customLabels';
import './styles.css';

class SearchInput extends React.Component {
    constructor () {
        super();

        this.state = {
            searchTerm: ''
        };

        this.__doSearch = debounce(this.__doSearch, 500);
    }

    __doSearch() {
        this.props.doSearch(this.state.searchTerm);
    }


    __handleSearch(event) {
        this.setState({ searchTerm: event.target.value }, () => {
            this.__doSearch();
        });
    }

    render () {
        return (
            <div>
                <svg aria-hidden="true" className="slds-icon search-icon">
                    <use href={lsdIcons.search}></use>
                </svg>
                <input
                className="searchInput"
                type="search"
                placeholder={labels.search_work_types}
                value={this.state.searchTerm}
                onChange={this.__handleSearch.bind(this)} />
            </div>
        )
    }
}

export default SearchInput;