import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import lsdIcons from '../../../../utils/lsdIcons';
import './styles.css';
import InlineResourceFieldEdit from "./InlineResourceFieldEdit/InlineResourceFieldEdit";
import LicensesToggles from "../LicensesToggles/LicensesToggles";
import { openSObjectLink } from '../../../../utils/Utils';


export default class ResourcesTable extends Component {

    constructor() {
        super();

        this.state = {
            sortBy: 'Name',
            order: 1,
            selectedRows: {},
            toggleAll: false
        };
    }


    render() {

        let tableRows = this.props.users
            .sort(this.sortRows.bind(this))
            .filter(this.isRowVisible.bind(this))
            .map(user => {

                    let rowClasses = this.state.selectedRows[this.props.resources[user.Id].Id] ? 'rd-table-row rd-table-selected' : 'rd-table-row';

                    return (
                        <div className={rowClasses}>

                            <div className="rd-table-cell rd-table-cell-selected">
                                <input type="checkbox"
                                       checked={this.state.selectedRows[this.props.resources[user.Id].Id]}
                                       onClick={this.onRowClicked.bind(this, this.props.resources[user.Id].Id)} />
                            </div>

                            <div className="rd-table-cell">
                                {this.generateResourceCellContent(user.Id)}
                            </div>

                            <div className="rd-table-cell rd-table-cell-user">
                                {user.Name}
                                <span title={labels.Open_User_Record} onClick={this.__openSObjectLink.bind(this, user.Id)}>
                                    <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.open} /></svg>
                                </span>
                            </div>



                            <div className="rd-table-cell">
                                { this.props.resources[user.Id] ?
                                    <InlineResourceFieldEdit value={this.props.resources[user.Id].GanttLabel} type="GanttLabel" resourceId={this.props.resources[user.Id].Id} onSave={this.inlineValueChanged.bind(this)} /> : '' }
                             </div>

                            <div className="rd-table-cell">
                                {this.generateTerritoryCellContent(user.Id)}
                            </div>

                            <div className="rd-table-cell rd-table-cell-small">
                                <InlineResourceFieldEdit value={this.props.resources[user.Id].IsActive} type="IsActive" resourceId={this.props.resources[user.Id].Id} onSave={this.inlineValueChanged.bind(this)} />
                            </div>


                            <div className="rd-table-cell rd-table-cell-small rd-resource-licenses">
                                <LicensesToggles fsl={this.getUserFslLicense(user.Id)} mobile={this.getUserMobileLicense(user.Id)} onChange={this.licenseChanged.bind(this, user.Id)} />
                            </div>
                        </div>
                    );
                });



        return (
            <div id="rd-the-table">

                <div className="rd-user-table">

                    <div className="rd-table-head">
                        <div className="rd-table-head-cell rd-table-head-selected"><input type="checkbox" checked={this.state.toggleAll} onClick={this.toggleAllRows.bind(this)} /></div>
                        <div className="rd-table-head-cell" onClick={this.setSortAndOrder.bind(this, 'Name')} >{this.styleColumn(labels.TableResourceName, 'Name')}</div>
                        <div className="rd-table-head-cell rd-table-head-user" onClick={this.setSortAndOrder.bind(this, 'user')} >{this.styleColumn(labels.TableUsername, 'user')}</div>
                        <div className="rd-table-head-cell" onClick={this.setSortAndOrder.bind(this, 'GanttLabel')} >{this.styleColumn(labels.TableGanttLabel, 'GanttLabel')}</div>
                        <div className="rd-table-head-cell" onClick={this.setSortAndOrder.bind(this, 'ServiceTerritory')} >{this.styleColumn(labels.TableTerritory, 'ServiceTerritory')}</div>
                        <div className="rd-table-head-cell rd-table-head-cell-small" onClick={this.setSortAndOrder.bind(this, 'IsActive')} >{this.styleColumn(labels.TableActive, 'IsActive')}</div>
                        <div className="rd-table-head-cell rd-table-head-cell-small" onClick={this.setSortAndOrder.bind(this, 'licenses')} >{this.styleColumn(labels.TableLicenses, 'licenses')}</div>
                    </div>


                    <div className="rd-table-body">
                        {tableRows}
                    </div>

                </div>

            </div>
        )
    }


    styleColumn(label, col) {

        if (col === this.state.sortBy) {
            let orderBy = this.state.order === 1 ?
                (<svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.arrowup} /></svg>)
                :
                <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.arrowdown} /></svg>;
            return (<b>{label}{orderBy}</b>);
        }

        return label;

    }


    generateResourceCellContent(uId) {

        if (this.props.resources[uId] === undefined) {
            return '';
        }

        return (
            <span>

                <InlineResourceFieldEdit value={this.props.resources[uId].Name} type="resourceName" resourceId={this.props.resources[uId].Id} onSave={this.inlineValueChanged.bind(this)} />

                <span title={labels.Open_Service_Resource_Record} onClick={this.__openSObjectLink.bind(this, this.props.resources[uId].Id)}>
                    <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.open} /></svg>
                </span>
            </span>
        );

    }


    generateTerritoryCellContent(uId) {

        if (!this.props.resources[uId] || !this.props.territoryMembers[this.props.resources[uId].Id]) {
            return '';
        }

        return (<span>
            {this.props.territoryMembers[this.props.resources[uId].Id].name}
            <span title={labels.Open_Service_Territory_Record} onClick={this.__openSObjectLink.bind(this, this.props.territoryMembers[this.props.resources[uId].Id].id)}>
                <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.open} /></svg>
            </span>
        </span>);

    }


    isRowVisible(user) {

        // user not active
        if (!user.IsActive) {
            return false;
        }

        // user with no resource
        if (!this.props.resources[user.Id]) {
            return false;
        }

        // no filtering
        if (!this.props.search) {
            return true;
        }

        // filter applied
        if (this.props.search &&
            (
                (this.props.resources[user.Id].Name.toUpperCase().indexOf(this.props.search.toUpperCase()) > -1)
                || (this.props.resources[user.Id].GanttLabel.toUpperCase().indexOf(this.props.search.toUpperCase()) > -1)
                || (user.Name && user.Name.toUpperCase().indexOf(this.props.search.toUpperCase()) > -1)
            )
        ) {
            return true;
        }

        return false;
    }


    getUserFslLicense(userId) {

        let fslLicenseId = this.props.licenseTypes['FieldServiceSchedulingPsl'];

        if (this.props.assignedLicenses[userId] && this.props.assignedLicenses[userId][fslLicenseId]) {
            return this.props.assignedLicenses[userId][fslLicenseId];
        }

        return null;
    }

    getUserMobileLicense(userId) {

        let mobileLicenseId = this.props.licenseTypes['FieldServiceMobilePsl'];

        if (this.props.assignedLicenses[userId] && this.props.assignedLicenses[userId][mobileLicenseId]) {
            return this.props.assignedLicenses[userId][mobileLicenseId];
        }

        return null;
    }


    // 3 - both exist, 2 - u2 exist, 1 - u1 exist, 0 - no resources
    checkIfUsersHaveResource(u1, u2) {

        if (this.props.resources[u1.Id] && this.props.resources[u2.Id]) {
            return 3;
        }

        if (!this.props.resources[u1.Id] && this.props.resources[u2.Id]) {
            return 2;
        }

        if (this.props.resources[u1.Id] && !this.props.resources[u2.Id]) {
            return 1;
        }

        if (!this.props.resources[u1.Id] && !this.props.resources[u2.Id]) {
            return 0;
        }

    }


    setSortAndOrder(field) {

        if (this.state.sortBy === field) {
            this.setState({ order: this.state.order * -1});
        } else {
            this.setState( {sortBy: field, order: 1});
        }

    }

    // 3 - both licenses, 2 - 2nd lisence in licensesTypes, 1 - 1st lisence in licensesTypes, 0 - no licenses, -1 - no resource assigned to user
    getLicenseScoreForSort(u) {

        if (!this.props.assignedLicenses[u.Id]) {
            return -1;
        }

        if (Object.keys(this.props.assignedLicenses[u.Id]).length === 2) {
            return 3;
        }

        if (this.props.assignedLicenses[u.Id][this.props.licenseTypes.FieldServiceSchedulingPsl]) {
            return 2
        }

        if (this.props.assignedLicenses[u.Id][this.props.licenseTypes.FieldServiceMobilePsl]) {
            return 1
        }

        if (Object.keys(this.props.assignedLicenses[u.Id]).length === 0) {
            return 0;
        }

    }

    getResourceFieldForSort(u) {
        return this.props.resources[u.Id][this.state.sortBy];
    }



    sortRows(u1, u2) {

        if (this.state.sortBy === 'ServiceTerritory') {

            let t1 = this.props.resources[u1.Id] && this.props.territoryMembers[this.props.resources[u1.Id].Id] ? this.props.territoryMembers[this.props.resources[u1.Id].Id].name : '',
                t2 = this.props.resources[u2.Id] && this.props.territoryMembers[this.props.resources[u2.Id].Id] ? this.props.territoryMembers[this.props.resources[u2.Id].Id].name : '';

            if (t1 === '' && t2 === '') {
                return 0;
            }

            if (t1 === '' && t2 !== '') {
                return this.state.order * -1;
            }

            if (t1 !== '' && t2 === '') {
                return this.state.order;
            }

            if (t1.toUpperCase() > t2.toUpperCase()) { return this.state.order }
            if (t1.toUpperCase() < t2.toUpperCase()) { return this.state.order * -1 }
            return 0;
        }


        if (this.state.sortBy === 'user') {
            return u1.Name.toUpperCase() >= u2.Name.toUpperCase() ? this.state.order : this.state.order * -1;
        }


        if (this.state.sortBy === 'licenses') {

            let u1score = this.getLicenseScoreForSort(u1),
                u2score = this.getLicenseScoreForSort(u2);

            if (u1score > u2score) { return this.state.order }
            if (u1score < u2score) { return this.state.order * -1 }
            return 0;

        }


        let resourceExist = this.checkIfUsersHaveResource(u1,u2);


        if (resourceExist === 0) { return 0; }
        if (resourceExist === 1) { return this.state.order; }
        if (resourceExist === 2) { return this.state.order * -1; }
        if (resourceExist === 3) {
            let u1Val = this.getResourceFieldForSort(u1),
                u2Val = this.getResourceFieldForSort(u2);

            if (typeof u1Val === 'string') {
                if (u1Val.toUpperCase() > u2Val.toUpperCase()) { return this.state.order }
                if (u1Val.toUpperCase() < u2Val.toUpperCase()) { return this.state.order * -1 }
                return 0;
            } else {
                if (u1Val > u2Val) { return this.state.order }
                if (u1Val < u2Val) { return this.state.order * -1 }
                return 0;
            }

        }

    }


    inlineValueChanged(value, field, resourceId) {
        this.props.saveResource(value, field, resourceId);
    }


    licenseChanged(userId, type, isAdded, cb) {
        this.props.licenseChanged(userId, type, isAdded, cb);
    }


    onRowClicked(resourceId) {
        let selectedRows = Object.assign({}, this.state.selectedRows),
            selectedRowsIds = [];

        selectedRows[resourceId] = selectedRows[resourceId] ? !selectedRows[resourceId] : true;

        Object.keys(selectedRows).forEach( k => selectedRows[k] ? selectedRowsIds.push(k) : null);
        this.props.onRowSelected(selectedRowsIds);

        this.setState({selectedRows});
    }


    toggleAllRows() {

        let selectedRows = Object.assign({}, this.state.selectedRows),
            toggleAll = !this.state.toggleAll;


        this.props.users
            .filter(this.isRowVisible.bind(this))
            .forEach(user => {
                selectedRows[this.props.resources[user.Id].Id] = toggleAll;
            });


        this.props.onRowSelected(toggleAll ? Object.keys(selectedRows) : []);

        this.setState({selectedRows, toggleAll});
    }


    __openSObjectLink(id, ev) {
        ev.preventDefault();
        openSObjectLink(id);
    }

}