import React, { Component } from 'react';
import ObjectNames from '../../../../utils/ObjectNames';
import labels from '../../../../utils/customLabels';
import './styles.css';

const NORMALIZED_MAX_VALUE_FOR_OBJECTIVE = 100;

export default class ObjectivesEditor extends Component {

    constructor(props) {

        super(props);

        this.sliders = {};
        this.sliderElements = this.__createSlidersElements(props.objectives);

        this.__setAvailableGoals(props.objectives, props.goalsJunction, props.policyId);

        this.numberOfActiveObjective = 0;

    }


    render() {
        return (

            <div id="ObjectiveEditorContainer">

                <h2 className="objectives-h2">{labels.OBObjectives}</h2>
                <p className="objectives-p">
                    {labels.ObjectiveEditorTextIntro}
                </p>

                <div className="objective-sliders-container">
                    {this.sliderElements}
                </div>


                <div id="pie-chart-container">
                    <div id="objectives-pie"></div>
                </div>

            </div>
        );
    }



    componentWillReceiveProps(nextProps) {

        this.__setAvailableGoals(nextProps.objectives, nextProps.goalsJunction, nextProps.policyId);

        // update slider values
        nextProps.objectives.forEach(obj => {
            this.sliders[obj.Id].setValue(this.objectiveWeights[obj.Id]);
        });

        // update pie
        this.__updatePie(nextProps.objectives);

    }


    componentDidMount() {

        this.props.objectives.forEach(obj => {

            this.sliders[obj.Id] = new window.dhtmlXSlider({
                parent: "obj_" + obj.Id,
                size:   340,
                step:   1,
                min:    0,
                max:    100,
                value:  this.objectiveWeights[obj.Id]
            });

            this.sliders[obj.Id].linkTo('sld_value_' + obj.Id);

            this.sliders[obj.Id].attachEvent("onSlideEnd", (newValue) => {
                let normalizedValue = ObjectivesEditor.normalize(newValue, this.maxWeightNotNormalized, NORMALIZED_MAX_VALUE_FOR_OBJECTIVE);
                this.__handleValueChanged(normalizedValue, obj.Id);
            });
        });


        // init pie chart
        this.__createPie(this.props.objectives);

    }


    __createSlidersElements(objectives) {

        return objectives.map(objective => {

            return (
                <div className="slider-container">
                    <div>
                        {objective.Name}
                        <span className="slider-value" id={'sld_value_' + objective.Id}></span>
                    </div>
                    <div id={'obj_' + objective.Id}></div>
                    <span className="slider-val">0</span>
                    <span className="slider-val slider-val-max">{NORMALIZED_MAX_VALUE_FOR_OBJECTIVE}</span>
                </div>
            );


        });

    }


    __setAvailableGoals(objectives, goalsJunctions, policyId) {

        this.objectiveWeights = {};

        objectives.forEach(obj => {

            this.objectiveWeights[obj.Id] = 0;

            goalsJunctions.forEach(junction => {

               if (policyId === junction.SchedulingPolicy && obj.Id === junction.ServiceGoal) {
                   this.objectiveWeights[obj.Id] = junction.Weight;
               }

            });
        });

        this.numberOfActiveObjective = Object.keys(this.objectiveWeights).length;

        this.__normalizeWeights();
    }

    __normalizeWeights() {

        this.maxWeightNotNormalized = 0;

        // find max weight
        for (let key in this.objectiveWeights) {
            if (this.objectiveWeights[key] > this.maxWeightNotNormalized) {
                this.maxWeightNotNormalized = this.objectiveWeights[key];
            }
        }

        // if we have no objectives set
        if (this.maxWeightNotNormalized === 0) {
            this.maxWeightNotNormalized = NORMALIZED_MAX_VALUE_FOR_OBJECTIVE;
        }

        for (let key in this.objectiveWeights) {
            this.objectiveWeights[key] = ObjectivesEditor.normalize(this.objectiveWeights[key] , NORMALIZED_MAX_VALUE_FOR_OBJECTIVE, this.maxWeightNotNormalized);
        }

    }


    __handleValueChanged(newValue, objectiveId) {

        let objectiveJunction = null;


        // find the junction
        this.props.goalsJunction.forEach(junction => {
            if (this.props.policyId === junction.SchedulingPolicy && objectiveId === junction.ServiceGoal && !objectiveJunction) {
                objectiveJunction = junction;
            }
        });


        // removing junction
        if (newValue === 0) {

            this.props.onSave('delete', objectiveJunction.Id);

        } else {

            let objectiveJunctionToSave = {
                Id: (objectiveJunction && objectiveJunction.Id) || null
            };

            objectiveJunctionToSave[ObjectNames.SchedulingPolicyGoal.SchedulingPolicy] = this.props.policyId;
            objectiveJunctionToSave[ObjectNames.SchedulingPolicyGoal.Service_Goal] = objectiveId;
            objectiveJunctionToSave[ObjectNames.SchedulingPolicyGoal.Weight] = newValue;

            this.props.onSave('upsert', objectiveJunctionToSave);

        }
    }


    static normalize(value, maxNew, maxOld) {

        if (maxOld <= NORMALIZED_MAX_VALUE_FOR_OBJECTIVE) {
            return value;
        }

        return (maxNew / maxOld) * (value - maxOld) + maxNew;
    }


    __createPie(objectives) {

        this.__preparePieData(objectives);


        this.objectivesPie = new window.dhtmlXChart({
            view: "pie",
            container: "objectives-pie",
            value: "#weight#",
            color: "#color#",
            pieInnerText: "#weight#",
            labelLines: true,
            shadow: 0,
            //label: "#label#"
            //label: obj => `<div class="oe-graph-label" title="${obj.label}">${obj.label}</div>`
            label: obj => `<div class="oe-graph-label" title="${obj.label}">${obj.label}</div>`
        });

        this.objectivesPie.parse(this.pieData,"json");
    }

    __preparePieData(objectives) {

        let colors = {},
            colorsHex = ['#fe8f61', '#db6d7a', '#88c551', '#34bbe7', '#ffc35f', '#e46fbe', '#8e7cc3', '#fa975c', '#b7c3cd', '#20ca9f', '#5a9cdc', '#f6675b', '#e25c80'],
            currentColor = 0;


        let sumOfWeights = 0;
        for (let key in this.objectiveWeights) {
            sumOfWeights += this.objectiveWeights[key];
        }

        this.pieData = [];

        objectives.forEach(obj => {

            colors[obj.Id] = colorsHex[currentColor++];

            if (currentColor === colorsHex.length) {
                currentColor = 0;
            }

            if (this.objectiveWeights[obj.Id] === 0) {
                return;
            }

            this.pieData.push({
                id: obj.Id,
                weight: ((this.objectiveWeights[obj.Id] / sumOfWeights) * 100).toFixed(2) + '%',
                label: obj.Name,
                color: colors[obj.Id]
            });
        });


    }


    __updatePie(objectives) {
        this.objectivesPie.clearAll();
        this.__preparePieData(objectives);
        this.objectivesPie.parse(this.pieData, 'json');
    }


}