// remote actions, defined in OnboardingRemoteActions.component

function callRemoteAction(remoteActionName, ...params) {

    let promise = new Promise( (resolve, reject) => {

        let escape = window.shouldEscapeResult.indexOf(remoteActionName) > -1;

        let remoteActionsParams = [window.remoteActions[remoteActionName]]
            .concat(params)
            .concat(function(data, ev) {

                if (!remoteActionName.includes('get')) {

                    callRemoteAction.operations--;

                    if (callRemoteAction.setComponenetSavingStatus) {
                        callRemoteAction.setComponenetSavingStatus(!!callRemoteAction.operations);
                    }
                }


                ev.status ? resolve(data) : reject(ev);
            })
            .concat({ buffer: false, escape: escape });

        remoteActionsParams = remoteActionsParams.filter(function(v) { return v !== undefined });

        window.Visualforce.remoting.Manager.invokeAction.apply(window.Visualforce.remoting.Manager, remoteActionsParams);

        if (!remoteActionName.includes('get')) {
            callRemoteAction.operations++;

            if (callRemoteAction.setComponenetSavingStatus) {
                callRemoteAction.setComponenetSavingStatus(!!callRemoteAction.operations);
            }
        }


    });

    return promise;

}

callRemoteAction.operations = 0;


export default callRemoteAction;