import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css';
import Lightbox from "../../../Lightbox/Lightbox";
import callRemoteAction from '../../../../utils/callRemoteAction';
import lsdIcons from '../../../../utils/lsdIcons';
import Tooltip from "../../../Tooltip/Tooltip";

export default class ResourcesBulkActions extends Component {

    constructor(props) {
        super(props);

        this.state = {
            filter: '',
            saving: false,
            showEditLightbox: false,
            skills: [],
            action: 'add',
            form: {
                skills: {}
            }
        };

        callRemoteAction('getSkills').then(skills => {
            this.setState({skills});
        });

        this.tooltipTexts = {
            add: labels.SkillsAddedTooltip,
            overide: labels.SkillsAddedNotRemoved,
            remove: labels.SkillsRemovedTooltip,
        }
    }


    render() {
        if (this.props.selectedIds.length > 0) {

            let saving = null;

            if (this.state.saving) {
                saving = (<div className="rba-saving">
                    <img src={lsdIcons.spinner} />
                    {labels.Save_Skills}
                </div>)
            }



            return (
                <span>
                    <span id="rba-bulk" className="guided-setup-button guided-setup-button_brand" onClick={this.__toggleLightbox.bind(this)}>
                        {labels.UpdateXResources.replace('$0', this.props.selectedIds.length)}
                    </span>

                    <Lightbox show={this.state.showEditLightbox} height="425px" width="850px" handleVisibility={this.__toggleLightbox.bind(this)}>

                        <div className="rba-skills-container">
                            <h1>
                                <select value={this.state.action} onChange={this.__actionChanged.bind(this)}>
                                    <option value="add">{labels.AddSkillsOb}</option>
                                    <option value="remove">{labels.RemoveSkillsOb}</option>
                                    <option value="overide">{labels.OverideSkillsOb}</option>
                                </select>

                                {labels.AddResourceSkillss}
                                <Tooltip text={this.tooltipTexts[this.state.action]} />
                            </h1>

                            <input type="search" placeholder={labels.Search_Skills} value={this.state.filter} onChange={ e => this.setState({filter: e.target.value}) } />

                            <div className="rba-skills">
                                {this.__generateSkills()}
                            </div>
                        </div>

                        {saving}

                        <div id="rba-button" className="guided-setup-button guided-setup-button_brand" onClick={this.__updateResources.bind(this)}>
                            {labels.UpdateResources}
                        </div>


                    </Lightbox>

                </span>
            )
        } else {
            return null;
        }
    }


    __actionChanged(e) {
        this.setState({action: e.target.value});
    }

    __toggleLightbox() {

        let form = {
            skills: {}
        };

        if (!this.state.showEditLightbox && this.props.selectedIds.length === 1) {


            callRemoteAction('getResourceData', this.props.selectedIds[0]).then(result => {

                let form = Object.assign({}, this.state.form);
                form.skills = {};

                result.skills.forEach( s => {
                    form.skills[s.SkillId] = true;
                });

                this.setState({action: 'overide', form});

            });

        }

        this.setState({filter: '', action: 'add', saving: false, form, showEditLightbox: !this.state.showEditLightbox});


    }


    __generateSkills() {

        return this.state.skills
            .filter( skill => !this.state.filter === '' || skill.MasterLabel.toUpperCase().indexOf(this.state.filter.toUpperCase()) > -1)
            .map( skill => {
            return (
                <div className="rba-skill-container">
                    <input type="checkbox" id={'sk_' + skill.Id} checked={this.state.form.skills[skill.Id]} onClick={this.__skillChanged.bind(this, skill.Id)} />
                    <label className="rba-skill-name" htmlFor={'sk_' + skill.Id}>{skill.MasterLabel}</label>
                </div>
            );
        })

    }


    __skillChanged(id, e) {

        let form = Object.assign({}, this.state.form);
        form.skills[id] = e.target.checked;

        this.setState({form});
    }


    __updateResources() {

        if (this.state.saving) {
            return;
        }


        if (this.state.action != 'add' && !window.confirm(labels.DestructiveActionConfirm)) {
            return;
        }



        let skills = [];

        for (let id in this.state.form.skills) {
            if (this.state.form.skills[id]) {
                skills.push(id);
            }
        }

        this.setState({saving: true});

        callRemoteAction('updateSkills', this.props.selectedIds, skills, this.state.action).then(result => {
            this.setState({showEditLightbox:false, saving: false});
        }).catch(ex => {
            this.setState({showEditLightbox:false, saving: false});
            console.error(ex);
            window.alert(ex.message);
        })


    }



}