import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import labels from '../../../utils/customLabels';
import lsdIcons from '../../../utils/lsdIcons';
import Data_WorkType from '../../../data/WorkType';
import Data_Skills from '../../../data/Skills';
import WorkTypesList from './WorkTypesList/WorkTypesList';
import WorkTypeForm from './WorkTypeForm/WorkTypeForm';
import './styles.css';


class WorkTypesAndSkills extends Component {

    constructor() {
        super();
        this.state = {
            selectedWorkType: null,
            lastSaved: null,
            workTypes: null,
            skills: null,
        };

        this.icon = {
            url: lsdIcons.work_type,
            style: {
                background: '#5876a3'
            }
        }
    }

    componentDidMount() {

        Data_WorkType.get().then(worktypes => {

            this.setState(Object.assign({}, {workTypes: worktypes}));

        });

        Data_Skills.get().then(skills => {

            this.setState(Object.assign({}, {skills: skills}));

        });
    }

    render() {

        return (
            <div className="full-height">
                <PageHeader title={labels.WorkTypesAndSkills}
                            text={labels.WorkTypesAndSkillsExplanation}
                            icon={this.icon}/>

                <WorkTypesList
                    selectWorkType={this.__handleSelectWorkType.bind(this)}
                    afterSave={this.__handleFormSaved.bind(this)}
                    workTypes={this.state.workTypes} />

                <WorkTypeForm workType={this.state.selectedWorkType}
                              afterSave={this.__handleFormSaved.bind(this)}
                              workTypes={this.state.workTypes}
                              skills={this.state.skills} />
            </div>
        );

    }

    __handleSelectWorkType(worktype) {
        this.setState({ selectedWorkType: worktype })
    }

    __handleFormSaved(worktype) {

        if (!window.Array.isArray(worktype)) {
            worktype = [worktype];
        }

        //find changed wt
        let wts = this.state.workTypes;
        for (let i=0; i<wts.length; i++){
            if (worktype[0].Id === wts[i].Id)
                wts[i] = worktype[0];
        }


        this.setState({
            lastSaved: worktype[0],
            workTypes: wts,
            selectedWorkType: worktype[0]
        })

        // this.setState(Object.assign(
        //         {},
        //         this.state, {lastSaved: worktype},
        //         {workTypes: [...wts]}, {selectedWorkType: worktype}
        //     )
        // );
    }

    static generateIntroText() {
        return (
            <span>
                <ul>
                    <li>{labels.WorkTypesAndSkillsPoint1}</li>
                    <li>{labels.WorkTypesAndSkillsPoint3}</li>
                    <li>{labels.WorkTypesAndSkillsPoint2}</li>
                    <li>{labels.WorkTypesAndSkillsPoint4}</li>
                    <li>{labels.WorkTypesAndSkillsPoint5}</li>
                </ul>
            </span>
        );
    }

}

export default WorkTypesAndSkills;