import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import labels from '../../../utils/customLabels';
import PoliciesList from './PoliciesList/PoliciesList';
import policies from '../../../data/Policies';
import PolicyForm from './PolicyForm/PolicyForm';
import RulesSelector from "./RulesSelector/RulesSelector";
import ObjectivesEditor from "./ObjectivesEditor/ObjectivesEditor";
import lsdIcons from '../../../utils/lsdIcons';
import callRemoteAction from '../../../utils/callRemoteAction';
import debounce from 'lodash/debounce';
import './styles.css';
import AddPolicy from "./AddPolicy/AddPolicy";



export default class SchedulingPolicies extends Component {

    constructor() {
        super();

        this.icon = {
            url: lsdIcons.policy,
            style: {
                background: '#d15c96'
            }
        };

        this.__generatePageHeader(); // will be saved to: this.pageHeader

        this.state = {
            selected: null,
            sobjects: {},
        };

        policies.get().then(this.__setSobjectsOnState.bind(this));

        this.__handleFormSaved = debounce(this.__handleFormSaved, 2000);
    }

    render() {

        // not yet loaded
        if (Object.keys(this.state.sobjects).length === 0) {

            return (
                <div>{this.pageHeader}</div>
            );

        // loaded data but no policy is selected
        } else if ((Object.keys(this.state.sobjects).length > 0) && (this.state.selected === null)) {

            return (
                <div>
                    {this.pageHeader}

                    <div id="PoliciesManagementList">
                        <AddPolicy onNewPolicy={ this.__handleNewPolicyAdded.bind(this) } />
                        { this.__generagePoliciesList() }
                    </div>

                    <div id="PolicyForm">
                        <div className="no-policy-selected">{labels.SelectPolicyFromTheLeft}</div>
                    </div>

                </div>
            );

        }

        // policy is selected
        return (
            <div>

                {this.pageHeader}

                <div id="PoliciesManagementList">
                    <AddPolicy onNewPolicy={ this.__handleNewPolicyAdded.bind(this) } />
                    { this.__generagePoliciesList() }
                </div>


                <div id="PolicyForm">

                    <PolicyForm
                        selected={this.state.selected}
                        formSavedCallback={this.__handleFormSaved.bind(this)} />


                    <RulesSelector
                        policyId={this.state.selected.Id}
                        rules={this.state.sobjects.rules}
                        onAdded={this.__handlePolicyRulesChanged.bind(this)}
                        onRemoved={this.__handlePolicyRulesChanged.bind(this)}
                        rulesJunction={this.state.sobjects.rulesJunction} />


                    <ObjectivesEditor
                        policyId={this.state.selected.Id}
                        objectives={this.state.sobjects.goals}
                        goalsJunction={this.state.sobjects.goalsJunction}
                        onSave={this.__handleObjectiveChange.bind(this)} />

                </div>

            </div>
        );

    }


    __reselectPolicyAfterFetching() {

        if (!this.state.selected) {
            return
        }


        let policies = this.state.sobjects.policies,
            selectedId = this.state.selected.Id

        for (let i=0; i<policies.length; i++) {
            if (policies[i].Id === selectedId) {
                this.setState({selected: policies[i]});
                break;
            }
        }

    }


    __handlePolicySelected(selectedId) {

        if (this.state.selected && selectedId === this.state.selected.Id) {
            return;
        }

        let policies = this.state.sobjects.policies;

        for (let i=0; i<policies.length; i++) {
            if (policies[i].Id === selectedId) {
                this.setState({selected: policies[i]});
                break;
            }
        }

    }

    __handleNewPolicyAdded(name, callback) {

        callRemoteAction('addPolicy', name).then(newPolicies => {

            policies.parseAllPoliciesObjects(newPolicies);
            this.__setSobjectsOnState( policies.getNow() );
            this.__reselectPolicyAfterFetching();
            callback();

        });
    }


    __clonePolicy(id) {

        callRemoteAction('clonePolicy', id).then(newPolicies => {
            policies.parseAllPoliciesObjects(newPolicies);
            this.__setSobjectsOnState( policies.getNow() );
            this.__reselectPolicyAfterFetching();
        });

    }


    __handleFormSaved(id, name, desc, isInday, commitMode, isFixOverlaps) {



        callRemoteAction('upsertPolicy', id, name, desc, isInday, commitMode, isFixOverlaps).then(policy => {

            let p = policies.parsePolicy(policy);
            policies.upsertPolicy(p);
            this.setState({ sobjects: policies.getNow(), selected: p });

        });

    }


    __handlePolicyRulesChanged(objects, deleteOrAdd, callback) {

        let remoteActionToCall = deleteOrAdd === 'add' ? 'addNewWorkRulePolicyJunction' : 'removeRulesFromPolicy';

        if (deleteOrAdd === 'remove') {

            let newObjects = [];

            objects.forEach(id => {
                let junctionsIds = this.state.sobjects.rulesJunction.filter(junction => junction.SchedulingPolicy === this.state.selected.Id && junction.WorkRule === id);
                newObjects.push(...junctionsIds);
            });

            objects = newObjects.map(o => o.Id);

        }

        callRemoteAction(remoteActionToCall, objects).then(newJunctions => {

            let junctionMap = newJunctions.map(policies.parseRulePolicyJunction);

            let sobjects = Object.assign(
                {},
                this.state.sobjects,
                { rulesJunction: junctionMap}
            );

            policies.updatePolicies('rulesJunction', junctionMap);

            this.setState({sobjects});
            callback();

        });

    }


    __handleObjectiveChange(actionType, obj) {

        if (actionType === 'delete') {

            // here, obj is id to delete
            callRemoteAction('removeObjectiveFromPolicy', obj).then(didDelete => {

                if (didDelete) {
                    policies.deletePolicyObjectiveJuction(obj);
                    this.setState( { sobjects: policies.getNow() });
                }

            });

        }

        if (actionType === 'upsert') {

            callRemoteAction('upsertObjectiveToPolicy', obj).then(newJunction => {
                policies.upsertPolicyObjectiveJunction(newJunction[0]);
                this.setState( { sobjects: policies.getNow() });
            });

        }

    }


    __generatePageHeader() {

        this.pageHeader = (
            <PageHeader
                icon={this.icon}
                title={labels.SchedulingPolicies}
                text={labels.SchedulingPolicyOBInto}
                more={SchedulingPolicies.generateIntroText()} />
        );
    }


    __generagePoliciesList() {
        return (
            <PoliciesList
                selectedId={this.state.selected && this.state.selected.Id}
                policies={this.state.sobjects.policies}
                onClone={this.__clonePolicy.bind(this)}
                onSelected={this.__handlePolicySelected.bind(this)} />
        );
    }


    __setSobjectsOnState(data) {
        this.setState({
            sobjects: {
                goals: data.goals,
                goalsJunction: data.goalsJunction,
                policies: data.policies,
                rulesJunction: data.rulesJunction,
                rules: data.rules
            }
        });
    }


    static generateIntroText() {

        return (
            <span>
                <br/>
                {labels.SchedulingPolicyOBInto2}
                <ul>
                    <li>{labels.SchedulingPolicyOBPoint1}</li>
                    <li>{labels.SchedulingPolicyOBPoint2}</li>
                </ul>
                {labels.SchedulingPolicyOBEnding}
            </span>
        );

    }

}