import React, { Component } from 'react';
import './styles.css';
import labels from '../../utils/customLabels';


export default class PageHeader extends Component {

    constructor() {
        super();

        this.state = { showMore: false }

    }


    render() {

        let icon = this.props.icon ? (<svg aria-hidden="true" style={this.props.icon.style} className="slds-icon icon-on-header"><use href={this.props.icon.url}></use></svg>) : null,
            text = (<div><p>{this.props.text}</p><p>{this.props.secondParagraph}</p></div>);


        if (this.props.more && !this.state.showMore) {
            text = (
                <p>
                    {this.props.text}
                    <span className="show-more-less" onClick={this.__toggleMore.bind(this)}>{labels.ReadMore}</span>
                </p>);
        } else if (this.props.more && this.state.showMore) {
            text = (
                <p>
                    {this.props.text}
                    {this.props.more}
                    <span className="show-more-less" onClick={this.__toggleMore.bind(this)}>{labels.ReadLess}</span>
                </p>
            );
        }


        return (
            <div className="page-header">
                <h1>
                    {icon}
                    {this.props.title}
                </h1>
                {text}
            </div>
        )

    }


    __toggleMore() {
        this.setState({ showMore: !this.state.showMore});
    }

}