import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import labels from '../../../Utils/customLabels';
import OperatingHoursSelect from '../../OperatingHoursSelect/OperatingHoursSelect';
import policies from '../../../data/Policies';
import Lightbox from '../../Lightbox/Lightbox';
import callRemoteAction from '../../../utils/callRemoteAction';
import OperatingHoursCreator from '../../OperatingHoursCreator/OperatingHoursCreator';
import './styles.css';
import lsdIcons from '../../../utils/lsdIcons';
import SlotsPreview from "./SlotsPreview/SlotsPreview";
import SuccessMessage from "../../SuccessMessage/SuccessMessage";
import Tooltip from "../../Tooltip/Tooltip";



let timeSlotsCache = {};

export default class AppointmentBooking extends Component {

    constructor() {

        super();

        this.icon = {
            url: lsdIcons.appBooking,
            style: {
                background: '#3d96dd'
            }
        };

        this.state = {
            addNewOh: null,
            selectedOperatingHours: window.abSerttings.defaultOperatingHoursForAb,
            selectedPolicy: window.abSerttings.defaultPolicyForAb,
            policies: [],
            showWorkingHoursCreator: false,
            showPreview: null,
            showSuccess: false
        };

        policies.get().then(objects => {
            this.setState( { policies: objects.policies });
            this.__operationHoursChanged(window.abSerttings.defaultOperatingHoursForAb);
        });

        this.moreText = (<div>{labels.AppBookingMore1}</div>)

    }

    render() {


        let policyOptions = this.state.policies.map( policy => {
            return (<option value={policy.Id}>{policy.Name}</option>);
        });

        return (
            <div>

                <SuccessMessage show={this.state.showSuccess} text={labels.OpSavedGood} />

                <PageHeader title={labels.AppointmentBooking} text={labels.AppointmentBookingExplanation} icon={this.icon} secondParagraph={this.moreText}/>

                <div id="AppointmentBookingForm">


                    {/*<div id="Select-Default-Ab-Settings">{labels.SelectDefaultAbSettings}</div>*/}
                    {/*<div className="ab-explain-text">{labels.SelectDefaultAbSettingsExplain}</div>*/}

                    <div className="oh-form">
                        <div className="ohs-label">{labels.SchedulingPolicyName}
                            <Tooltip text={labels.GuidedSetup_appt_booking_policy_tooltip} />
                        </div>
                        <div className="ohs-input">
                            <select
                                onChange={this.__schedulingPolicyChanged.bind(this)}
                                value={this.state.selectedPolicy}>
                                {policyOptions}
                                </select>
                        </div>
                    </div>

                    <OperatingHoursSelect
                        multiselect={false}
                        addNewOh={this.state.addNewOh}
                        selected={this.state.selectedOperatingHours}
                        dontShowTz={true}
                        onChange={this.__operationHoursChanged.bind(this)} />

                    <div className="create-new-oh-ab" onClick={this.__toggleWorkingHoursCreator.bind(this)}>
                        {labels.CreateNewOH}
                    </div>

                    <Lightbox show={this.state.showWorkingHoursCreator} height="420px" width="680px" handleVisibility={this.__toggleWorkingHoursCreator.bind(this)}>
                        <OperatingHoursCreator hideTimezone={true} createSlots={true} afterSave={this.__afterSaveOperatingHours.bind(this)} />
                    </Lightbox>

                </div>


                <SlotsPreview slots={this.state.showPreview} />
            </div>
        );

    }


    __schedulingPolicyChanged(e) {

        let newPolicyId = e.target.value;

        this.setState( {selectedPolicy: newPolicyId});

        callRemoteAction('updateAbSettings', 'policy', newPolicyId).then(isUpdated => {

            window.abSerttings.defaultPolicyForAb = newPolicyId;

        }).catch(ex => {
            console.error(ex);
            this.setState( {selectedOperatingHours: window.abSerttings.defaultPolicyForAb});
        });



    }


    __operationHoursChanged(id) {

        if (this.state.selectedOperatingHours === id) {

            callRemoteAction('getTimeSlots', id).then(slots => {

                let preCachedSlots = {
                    Monday: [],
                    Tuesday: [],
                    Wednesday: [],
                    Thursday: [],
                    Friday: [],
                    Saturday: [],
                    Sunday: []
                };

                slots.sort((s1,s2) => s1.StartTime > s2.StartTime ? 1 : -1)
                    .forEach(slot => {
                        preCachedSlots[slot.DayOfWeek].push({
                            startHour:  Math.floor(slot.StartTime / 1000 / 60 / 60),
                            startMinutes:  Math.floor(slot.StartTime / 1000 / 60 % 60),
                            endHour:  Math.floor(slot.EndTime / 1000 / 60 / 60),
                            endMinutes:  Math.floor(slot.EndTime / 1000 / 60 % 60)
                        });
                    });

                // to guarantee order
                timeSlotsCache[id] = [
                    preCachedSlots.Monday, preCachedSlots.Tuesday, preCachedSlots.Wednesday,
                    preCachedSlots.Thursday,preCachedSlots.Friday, preCachedSlots.Saturday, preCachedSlots.Sunday
                ];

                this.setState({showPreview: timeSlotsCache[id]})

            });


            return;
        }

        this.setState( {selectedOperatingHours: id});

        callRemoteAction('updateAbSettings', 'hours', id).then(isUpdated => {

            window.abSerttings.defaultOperatingHoursForAb = id;

            if (timeSlotsCache[id]) {

                this.setState({
                    showPreview: timeSlotsCache[id],
                    selectedOperatingHours: id
                });

                return;
            }

            // if slots not cached, bring them
            // this.setState( {selectedOperatingHours: id});

            callRemoteAction('getTimeSlots', id).then(slots => {

                let preCachedSlots = {
                    Monday: [],
                    Tuesday: [],
                    Wednesday: [],
                    Thursday: [],
                    Friday: [],
                    Saturday: [],
                    Sunday: []
                };

                slots.sort((s1,s2) => s1.StartTime > s2.StartTime ? 1 : -1)
                .forEach(slot => {
                    preCachedSlots[slot.DayOfWeek].push({
                        startHour:  Math.floor(slot.StartTime / 1000 / 60 / 60),
                        startMinutes:  Math.floor(slot.StartTime / 1000 / 60 % 60),
                        endHour:  Math.floor(slot.EndTime / 1000 / 60 / 60),
                        endMinutes:  Math.floor(slot.EndTime / 1000 / 60 % 60)
                    });
                });

                // to guarantee order
                timeSlotsCache[id] = [
                    preCachedSlots.Monday, preCachedSlots.Tuesday, preCachedSlots.Wednesday,
                    preCachedSlots.Thursday,preCachedSlots.Friday, preCachedSlots.Saturday, preCachedSlots.Sunday
                ];

                this.setState({showPreview: timeSlotsCache[id]})

            });




        }).catch(ex => {
            console.error(ex);
            this.setState( {selectedOperatingHours: window.abSerttings.defaultOperatingHoursForAb});
        });






    }

    __toggleWorkingHoursCreator() {
        this.setState( {showWorkingHoursCreator: !this.state.showWorkingHoursCreator} );
    }

    __afterSaveOperatingHours(oh) {
        this.setState( {showWorkingHoursCreator: false, addNewOh: oh, showSuccess: true } );

        window.setTimeout(() => {
            this.setState( {showSuccess: false } );
        }, 3500)
    }

}