import React, { Component } from 'react';
import ObjectCounter from './ObjectCounter/ObjectCounter';
import lsdIcons from '../../../utils/lsdIcons';
import labels from '../../../utils/customLabels';
import callRemoteAction from '../../../utils/callRemoteAction';
import './styless.css';


export default class Home extends Component {

    constructor() {
        super();

        this.state = {
            counters: null
        };

        callRemoteAction('getObjectsCount').then(counters => {
            this.setState({counters})
        }).catch(ex => {
            console.error(ex);
        });

        this.icons = {
            policies:       { url: lsdIcons.policy, style: { fill: '#d15c96' } },
            territories:    { url: lsdIcons.territory, style: { fill: '#7e8be4' } },
            workTypes:      { url: lsdIcons.work_type, style: { fill: '#5876a3' } },
            objectives:     { url: lsdIcons.objectives, style: { fill: '#56aadf' } },
            operatingHours: { url: lsdIcons.operatingHours, style: { fill: '#6b9de2' } },
            resources:      { url: lsdIcons.resources, style: { fill: '#ef6e64' } },
            rules:          { url: lsdIcons.rules, style: { fill: '#45c073' } },
        }


        this.links = {
            policies:       "/SchedulingPolicies",
            territories:    "/DefineTerritories",
            workTypes:      "/WorkTypesAndSkills",
            objectives:     "/SchedulingPolicies",
            operatingHours: null,
            resources:      "/ResourcesDefenition",
            rules:          "/SchedulingPolicies",
        };


    }

    render() {

        let partCWithLink = labels.GuidedSetup_Welcome_walkthrough_partC.split('$0'); 

        return (
            <div>
                <div className="welcome-conaynet">
                    <h1>{labels.WelcomeOb}</h1>

                    <div>{labels.GuidedSetup_Welcome_walkthrough_partA}</div><br/>
                    <div>{labels.GuidedSetup_Welcome_walkthrough_partB}</div><br/>
                    <div>
                        <span>{partCWithLink[0]}</span>
                        <a className="setup-link" href="https://help.salesforce.com/articleView?id=fs_set_up.htm" target="_blank">{labels.GuidedSetup_Setup_link}</a>
                        <span>{partCWithLink[1]}</span>
                    </div>

                 </div>

                <div className="counters-container">
                    {this.__generateCounters()}
                </div>
            </div>
        );

    }


    __generateCounters() {

        if (!this.state.counters) {
            return null;
        }

        let counters = [];

        for (let key in this.state.counters) {
            counters.push((<ObjectCounter name={labels.objectNames[key]} count={this.state.counters[key]} icon={this.icons[key]} link={this.links[key]} />));
        }

        return counters;

    }

}