import React, { Component } from 'react';
import labels from '../../../../../utils/customLabels';
import callRemoteAction from '../../../../../utils/callRemoteAction';
import '../../../../../globalStyles.css';

class NewWorkType extends Component {

    constructor() {
        super();
        this.state = { addingDisabled: false };
    }

    render() {
        let buttonClass = this.state.addingDisabled ? 'guided-setup-button guided-setup-button_neutral add-new-disabled' : 'guided-setup-button guided-setup-button_neutral';

        return (
            <form className="add-new" onSubmit={this.__addNewWorkType.bind(this)}>
                <input placeholder={labels.addNewWorkTypePlaceholder} maxlength="100" type="text" ref={ input => { this.workTypeNameInput = input }} />
                <button className={buttonClass}>{labels.NewWorkTypeButton}</button>
            </form>
        );
    }


    __addNewWorkType(event) {
        event.preventDefault();

        if (this.state.addingDisabled) {
            return;
        }

        let name = this.workTypeNameInput.value;

        if (!this.props.validate(name)) {
            return;
        }

        if (name) {

            this.setState({ addingDisabled: true });

            callRemoteAction('addWorkType', name).then(workType => {
                console.log(workType);
                this.workTypeNameInput.value = '';
                this.props.onNewAdded(workType);
            }).catch(ex => {
                console.error(ex);
            }).then(() => {
                this.setState({ addingDisabled: false });
            });

        } else {
            alert(labels.WorkTypeNameCantBeBlank);
        }

    }

}

export default NewWorkType;