import React, { Component } from 'react';
import labels from '../../../../../utils/customLabels';
import callRemoteAction from '../../../../../utils/callRemoteAction';
import lsdIcons from '../../../../../utils/lsdIcons';
import Tooltip from "../../../../Tooltip/Tooltip";
import './styles.css';

class NewTerritory extends Component {

    constructor() {
        super();
        this.state = { addingDisabled: false };
    }

    render() {

        let buttonOrSpinner = <button className="guided-setup-button guided-setup-button_neutral" onClick={this.__addNewTerritory.bind(this)}>{labels.NewTerritoryButton}</button>;

        if (this.state.addingDisabled) {
            buttonOrSpinner = (
                <span className="add-new-saving">
                    <img src={lsdIcons.spinner}/>
                    {labels.SavingTerritory}
                </span>
            )
        }


        return (
            <div>
                <div className="add-new-territory">
                    <input type="text" disabled={this.state.addingDisabled} placeholder={labels.addNewTerritoryPlaceholder} maxlength="100" ref={ input => { this.territoryNameInput = input }} onKeyDown={this.__handleKeyboardAdd.bind(this)}/>
                    {buttonOrSpinner}
                </div>
                <Tooltip text={labels.GuidedSetup_territories_add_tooltip} />
            </div>
        );
    }


    __addNewTerritory() {

        if (this.state.addingDisabled) {
            return;
        }
        let name = this.territoryNameInput.value;

        if (!this.props.validate(name)) {
            return;
        }

        if (name) {

            this.setState({ addingDisabled: true });

            callRemoteAction('addServiceTerritory', name).then(territory => {
                this.territoryNameInput.value = '';
                this.props.onNewAdded(territory);
            }).catch(ex => {
                console.error(ex);
                if (ex.message.indexOf('System') > -1 && ex.message.indexOf('Class') > -1)
                    alert(ex.message.substring(ex.message.indexOf('System'), ex.message.indexOf('Class')));
                else
                    alert(labels.wentWrongContactSysAdmin);
            }).then(() => {
                this.setState({ addingDisabled: false });
            });


        } else {
            alert(labels.TerritoryNameCantBeBlank);
        }

    }

    __handleKeyboardAdd(e) {

        // if Enter is pressed
        if (e.which === 13) {
            this.__addNewTerritory();
        }
    }

}

export default NewTerritory;