import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import Tooltip from "../../../Tooltip/Tooltip";
import lsdIcons from '../../../../utils/lsdIcons';
import './styles.css';



export default class LicensesCounter extends Component {

    constructor() {
        super();
    }


    render() {

        return (<div className="licenses-container">
                    
                    <div id="rd-remaining-licenses">
                        {labels.LicenseRemaining}
                        {this.generateFslCounter()}
                        {this.generateMoibileCounter()}
                    </div>
                    <span className="license-tooltip-cont">
                        <Tooltip text={labels.Licenses_in_use_tooltip} />
                    </span>
                </div>
        );

    }


    generateFslCounter() {
        if (this.props.counters && this.props.counters.FieldServiceSchedulingPsl) {
            return (
                <div className="rd-remaining">
                    <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.fslLicense} /></svg>
                    {this.props.counters.FieldServiceSchedulingPsl.used}/{this.props.counters.FieldServiceSchedulingPsl.total}
                </div>);
        }
    }

    generateMoibileCounter() {
        if (this.props.counters && this.props.counters.FieldServiceMobilePsl) {
            return (
                <div className="rd-remaining">
                    <svg aria-hidden="true" className="slds-icon rd-mobile"><use href={lsdIcons.mobileLicense} /></svg>
                    {this.props.counters.FieldServiceMobilePsl.used}/{this.props.counters.FieldServiceMobilePsl.total}
                </div>);
        }
    }
}