import React, { Component } from 'react';
import './styles.css';


export default class SuccessMessage extends Component {

    constructor() {
        super();

    }


    render() {

        if (this.props.show) {
            return (
                <div className="ob-success-message">
                    {this.props.text}
                </div>
            )
        }


        return null;

    }

}