import callRemoteAction from '../utils/callRemoteAction';

class Skills {

    constructor() {
        this.synced = false;
    }

    get() {

        if (this.synced) {
            return new Promise( resolve => resolve(this.skills) );
        }


        return new Promise( (resolve, reject) => {

            callRemoteAction('getSkills').then(skills => {
                this.skills = skills;
                this.synced = true;
                resolve(this.skills);
            }).catch(ex => {
                this.synced = false;
                console.error('Unable to get skills');
                reject(ex);
            });

        });
    }

}

let skills = new Skills();

export default skills;
