import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import lsdIcons from '../../../../utils/lsdIcons';
import './styles.css';

export default class AgentDispatcherLicenses extends Component {

    constructor(props) {
        super(props);
        this.state = { saving: false };
    }


    render() {

        return(
            <div>
                {this.generateDispatcherLicense()}
                {this.generateAgentLicense()}
            </div>
        );

    }


    generateDispatcherLicense() {

        if (this.state.saving) {
            return <img src={lsdIcons.spinner} className="lt-saving-icon lt-saving-fsl" />;
        }

        let dispatcherLicense = this.doesHaveLicense('FieldServiceDispatcherPsl'),
            fslClasses = `slds-icon ad-licenseIcon ${dispatcherLicense ? 'dispatcherLicense' : ''}`;

        return (
            <span title={dispatcherLicense ? labels.RemoveDisLicnse : labels.AddDisLicnse}>
                <svg aria-hidden="true"
                     onClick={this.licenseChanged.bind(this,'dispatcher', !dispatcherLicense)}
                     className={fslClasses}>
                        <use href={lsdIcons.dispatcherLicense} />
                </svg>
            </span>
        )
    }


    generateAgentLicense() {

        // we draw loading sign on generateDispatcherLicense()
        if (this.state.saving) {
            return null;
        }

        let agentLicense = this.props.hasAgentPermissions,
            agentClasses = `slds-icon ad-licenseIcon ${agentLicense ? 'agentLicense' : ''}`;

        return (
            <span title={agentLicense ? labels.RemoveAgentLicnse : labels.AddAgentLicnse}>
                <svg aria-hidden="true"
                     onClick={this.licenseChanged.bind(this,'agent', !agentLicense)}
                     className={agentClasses}>
                        <use href={lsdIcons.agentLicense} />
                </svg>
            </span>
        )
    }


    licenseChanged(type, value) {

        if (!this.props.hasAgentPermissions || !this.doesHaveLicense('FieldServiceDispatcherPsl')) {

            if (type === 'dispatcher' && this.props.hasAgentPermissions) {
                alert(labels.LicenseConflictDispatcher);
                return;
            }


            if (type === 'agent' && this.doesHaveLicense('FieldServiceDispatcherPsl')) {
                alert(labels.LicenseConflictAgent);
                return;
            }

        }


        this.setState({saving: true});

        this.props.handleChange(type, value, () => {
            this.setState({ saving: false });
        });

    }


    doesHaveLicense(name) {

        if (!this.props.licenses) {
             return false;
        }


        for (let i=0; i<this.props.licenses.length; i++) {
            if (this.props.licenses[i].PermissionSetLicenseId === this.props.licensesIds[name]) {
                return true;
            }
        }

        return false;

    }

}
