import callRemoteAction from '../utils/callRemoteAction';

class OperatingHours {

    constructor() {
        this.synced = false;
    }

    get() {

        if (this.synced) {
            return new Promise( resolve => resolve(this.operatingHours) );
        }


        return new Promise( (resolve, reject) => {

            callRemoteAction('getOperatingHours').then(operatingHours => {
                this.operatingHours = operatingHours;
                this.synced = true;
                resolve(this.operatingHours);
            }).catch(ex => {
                this.synced = false;
                console.error('Unable to get operating hours');
                reject(ex);
            });

        });
    }


    addNewOh(oh) {
        if (this.operatingHours) {
            this.operatingHours.push(oh);
        }
    }


    getNow() {
        return this.operatingHours;
    }

}

let operatingHours = new OperatingHours();

export default operatingHours;