import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css';
import { Link } from 'react-router-dom';


export default class ObjectCounter extends Component {

    constructor(props) {
        super(props);

        this.count = 0;
        this.initCountAnimation(props.count);
    }

    render() {

        let counterTemplate = (
            <div className="object-counter-container">
                <svg aria-hidden="true" style={this.props.icon.style} className="slds-icon"><use href={this.props.icon.url}></use></svg>
                <div className="number-counter" ref={ i => this.counterElement = i}></div>
                <div className="counter-object-name">{this.props.name}</div>
            </div>
        );

        if (this.props.link) {
            return (<Link to={this.props.link}>{counterTemplate}</Link>);
        }

        return counterTemplate;
    }



    initCountAnimation(count) {

        let increaseBy = count < 100 ? 1 : 5,
            intervalSpeed = count < 100 ? 10 : 2;

        this.intervalId = setInterval(() => {
            if (this.count < count) {

                if (!this.counterElement) {
                    clearInterval(this.intervalId);
                    return;
                }

                this.counterElement.innerText = this.count += increaseBy;

                if (this.count > count) {
                    this.count = count;
                    this.counterElement.innerText = this.count;
                }

            } else {

                if (count === 0 && this.counterElement) {
                    this.counterElement.innerText = 0;
                } else {
                    clearInterval(this.intervalId);
                }

            }
        }, intervalSpeed);

    }

}