import React, { Component } from 'react';
import _ from 'lodash';
import labels from '../../../../../utils/customLabels';

import './styles.css';


class SkillsSelector extends Component {

    constructor() {
        super();

        this.sessionId = window.sessionId;
        this.soapEndPoint = window.soapEndPoint;
    }

    componentWillReceiveProps(newProps) {
        if (newProps) {
            this.existingSkills = _.map(newProps.requiredSkills, sk => sk.Skill['MasterLabel']);

            if (this.skillsBox) {
                this.skillsBox.removeAll();
                if (this.existingSkills)
                    this.skillsBox.add(this.existingSkills);

                this.generateSkillSelector();
            }
        }
    }

    componentDidMount() {
        //this.skillNames = _.map(this.props.skills, 'MasterLabel');
        this.skillNames = _.reduce(this.props.skills, (obj,item) => {
                                obj[item.MasterLabel] = item.Id;
                                return obj;
                            }, {});
        this.existingSkills = _.map(this.props.requiredSkills, sk => sk.Skill['MasterLabel']);

        this.generateSkillSelector();
    }

    generateSkillSelector() {
        if (!this.skillsBox) {

            this.skillsBox = new window.Taggle('skillsBox', {
                duplicateTagClass: 'bounce',
                placeholder: labels.EnterSkills,
                preserveCase: true,
                tags: this.existingSkills,
                onBeforeTagAdd: (ev, tag) => {

                    if (this.existingSkills.indexOf(tag) === -1 && !this.validSkill(tag)) {
                        window.alert(labels.SkillSpecialChars);
                        return false;
                    }

                   let found = _.has(this.skillNames, tag);
                   if (!found) {
                        if (!window.confirm(labels.ConfirmCreateNewSkill)) {
                            return false;
                        }
                        else {
                            this.__addSkill(ev, tag);
                        }
                   }
                },
                onTagAdd: (ev, tag) => {

                    if (this.existingSkills.indexOf(tag) > -1)
                        return;

                    // make sure tag added by keyboard click or by mouse click
                    if ( (ev && this.skillNames[tag])
                        || (window.event && (window.event.target.className.indexOf('ui-menu-item') > -1 && this.skillNames[tag]))) {
                        this.props.addRequiredSkill(this.skillNames[tag]);
                    }
                },
                onTagRemove: (ev, tag) => {
                    if (ev) {
                        this.props.removeRequiredSkill(_.find(this.props.requiredSkills, ['SkillId', this.skillNames[tag]]));
                    }
                }
            });

            let container = this.skillsBox.getContainer();
            let input = this.skillsBox.getInput();

            window.$(input).autocomplete({
                source: Object.keys(this.skillNames), // See jQuery UI documentaton for options
                appendTo: container,
                position: {at: "left bottom", of: container},
                delay: 500,
                select: (event, data) => {
                    event.preventDefault();
                    //Add the tag if user clicks
                    if (event.which === 1) {
                        this.skillsBox.add(data.item.value);
                    }
                }
            });

            // Overrides the default autocomplete filter function to
            // search only from the beginning of the string
            window.$.ui.autocomplete.filter = function (array, term) {
                var matcher = new RegExp("\\b" + window.$.ui.autocomplete.escapeRegex(term), "i");
                return window.$.grep(array, function (value) {
                    return matcher.test(value.label || value.value || value);
                });
            };
        }
    }

    __addSkill(event, tag) {

        if (!event) {
            return;
        }

        // W-10177153
        const url = window.location.origin + '/services/Soap/u/38.0';

        let data = `<?xml version="1.0" encoding="utf-8"?>
                        <SOAP-ENV:Envelope xmlns:SOAP-ENV="http://schemas.xmlsoap.org/soap/envelope/" xmlns:ns1="urn:sobject.partner.soap.sforce.com" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:ns2="urn:partner.soap.sforce.com">
                                <SOAP-ENV:Header>
                                    <ns2:SessionHeader>
                                        <ns2:sessionId>${this.sessionId}</ns2:sessionId>
                                    </ns2:SessionHeader>
                                </SOAP-ENV:Header>
                                <SOAP-ENV:Body>
                                    <ns2:create>

                                        <ns2:sObjects>
                                            <ns1:type>Skill</ns1:type>
                                            <ns1:MasterLabel>${tag}</ns1:MasterLabel>
                                            <ns1:DeveloperName>${window.escape(tag.split(' ').join('')).replace(/[_-]/g, "")}</ns1:DeveloperName>
                                        </ns2:sObjects>

                                    </ns2:create>
                                </SOAP-ENV:Body>
                        </SOAP-ENV:Envelope>`;

        window.$.ajax({
            url: url,
            type: 'POST',
            dataType: "xml",
            data: data,
            beforeSend: function (xhr) {
                xhr.setRequestHeader('Content-Type', 'text/xml');
                xhr.setRequestHeader('SOAPAction', '""');
            }
        }).then(
            ( res, textStatus, jqXHR ) => {
                if (textStatus == 'success') {
                    this.skillNames[tag] = window.$(jqXHR.responseText).find("id").text();
                    window.$(this.skillsBox.getInput()).autocomplete('option', 'source', Object.keys(this.skillNames));

                    this.props.addRequiredSkill(this.skillNames[tag]);
                }
            },
            ( jqXHR, textStatus, errorThrown ) => {
                event.stopPropagation();
                alert(errorThrown);
            }
        );
    }


    validSkill(name) {

        let nospcae = name.split(' ').join('');

        let re = /^\w+$/;
        return re.test(nospcae);
    }

    render() {
        return (
            <div className="skills-container">
                <div id="skillsBox" className="input textarea clearfix">

                </div>
            </div>
        )
    }

}

export default SkillsSelector;