import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css';
import lsdIcons from '../../../../utils/lsdIcons';
import AgentDispatcherLicenses from "../AgentDispatcherLicenses/AgentDispatcherLicenses";
import { openSObjectLink } from '../../../../utils/Utils';



export default class UserTerritoriesTable extends Component {


    constructor() {
        super();

        this.state = { asc: -1 }

    }


    render() {

        let arrow = this.state.asc === -1 ?
            (<svg aria-hidden="true" className="slds-icon cut-arrow"><use href={lsdIcons.arrowdown} /></svg>)
            :
            <svg aria-hidden="true" className="slds-icon cut-arrow"><use href={lsdIcons.arrowup} /></svg>;



        return (
            <div id="utt-table-container">

                <div className="utt-table-head">
                    <div className="utt-table-head-cell" onClick={e => this.setState({asc: this.state.asc * -1})}>{labels.UTuser} {arrow}</div>
                    <div className="utt-table-head-cell utt-territories-cell">{labels.UTterr}</div>
                    <div className="utt-table-head-cell utt-territories-cell-license">{labels.UTLicense}</div>
                </div>

                {this.__generateAllRows()}


            </div>
        );

    }



    __generateAllRows() {

        let userIds = Object.keys(this.props.userTerritories)
            .filter(k => this.__filterRow(this.props.userTerritories[k]))
            .sort( (a,b) => this.props.userTerritories[a][0].territoryName.toUpperCase() > this.props.userTerritories[b][0].territoryName.toUpperCase() ? this.state.asc * -1 : this.state.asc);

        let dispatcherPslId = this.props.licensesIds.FieldServiceDispatcherPsl;

        for (let al in this.props.assignedLicenses) {

            let licenses = this.props.assignedLicenses[al];

           if  ((licenses[0].PermissionSetLicenseId === dispatcherPslId) || (licenses[1] && licenses[1].PermissionSetLicenseId === dispatcherPslId)) {

               if (userIds.findIndex( v => v === al) === -1) {
                   userIds.push(al);
               }
           }
        }



        return userIds.map(k => {

            let user = this.props.users.find( u => u.Id === k),

                dummy = [{
                    Id: null,
                    territoryId: "",
                    territoryName: "",
                    userId: k,
                    active: user.IsActive,
                    userName: user.Name
                }];

            return this.__generateRow(this.props.userTerritories[k] || dummy, user.IsActive)
        });


        return Object.keys(this.props.userTerritories)
            .filter(k => this.__filterRow(this.props.userTerritories[k]))
            .sort( (a,b) => this.props.userTerritories[a][0].territoryName.toUpperCase() > this.props.userTerritories[b][0].territoryName.toUpperCase() ? this.state.asc * -1 : this.state.asc)
            .map(k => this.__generateRow(this.props.userTerritories[k]));


    }


    __generateRow(ut, isActive = true) {

        let territoriesNames = ut.map( u => u.territoryName ).join(', ');

        let notActiveHtml = !isActive ? <div className="notActiveUser"></div> : null;

        return (
            <div className="utt-table-row">

                <div className="utt-table-cell">
                    {ut[0].userName}
                    <a title={labels.Open_User_Record} onClick={this.__openSObjectLink.bind(this, ut[0].userId)}>
                        <svg aria-hidden="true" className="slds-icon"><use href={lsdIcons.open} /></svg>
                    </a>
                </div>
                <div className="utt-table-cell utt-territories-cell" title={territoriesNames}>
                    <div className="utt-territory-names-table">{territoriesNames}</div>
                    <span onClick={this.__editClicked.bind(this, ut[0].userId)}>{labels.UTEdit}</span>
                </div>
                <div className="utt-table-cell utt-territories-cell-license">
                    <AgentDispatcherLicenses hasAgentPermissions={this.props.agentPermissions[ut[0].userId]} licenses={this.props.assignedLicenses[ut[0].userId]} licensesIds={this.props.licensesIds} handleChange={this.__licenseChanged.bind(this, ut[0].userId)} />
                </div>
            </div>
        )

    }

    __licenseChanged(userId, licenseType, newValue, cb) {

        let action = newValue ? 'add' : 'remove',
            isDispatcher = licenseType === 'dispatcher';

        this.props.handleLicenseChanged(userId, action, isDispatcher, cb);

    }


    __filterRow(ut) {
        return ut[0].userName.toUpperCase().indexOf(this.props.filter.toUpperCase()) > -1;
    }


    __editClicked(userId) {
        this.props.editClicked(userId);
    }



    __openSObjectLink(id, ev) {
        ev.preventDefault();
        openSObjectLink(id);
    }


}