import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import TerritoriesTree from './TerritoriesTree/TerritoriesTree';
import TerritoryForm from './TerritoryForm/TerritoryForm';
import labels from '../../../utils/customLabels';
import lsdIcons from '../../../utils/lsdIcons';
import BulkTerritoriesActions from './BulkTerritoriesActions/BulkTerritoriesActions';
import callRemoteAction from '../../../utils/callRemoteAction';
import SuccessMessage from "../../SuccessMessage/SuccessMessage";


class DefineTerritories extends Component {

    constructor() {
        super();

        this.state = {
            selectedTerritory: null,
            lastSaved: null ,
            territories: null,
            showOpSuccess: false,
        };

        //this.moreText = (<div><ul><li>{labels.TerritoriesMore1}</li><li>{labels.TerritoriesMore2}</li></ul></div>);

        this.icon = {
            url: lsdIcons.territory,
            style: {
                background: '#7e8be4'
            }
        }
    }

    componentDidMount() {

        callRemoteAction('getServiceTerritories').then(territories => {
            this.setState(Object.assign({}, this.state, { territories }))
        }).catch(ex => {
            console.error(ex);
        });

    }

    render() {


        let rightSide = null;

        if (Array.isArray(this.state.selectedTerritory) && this.state.selectedTerritory.length > 1) {

            rightSide = (
                <BulkTerritoriesActions
                territories={this.state.selectedTerritory}
                afterOpAdded={this.__showSuccess.bind(this)}
                afterSave={this.__handleFormSaved.bind(this)} />
            );

        } else {
            rightSide = (<TerritoryForm
                territory={this.state.selectedTerritory}
                afterSave={this.__handleFormSaved.bind(this)}
                afterOpAdded={this.__showSuccess.bind(this)}
                territories={this.state.territories} />);
        }

        return (
            <div>

                <SuccessMessage show={this.state.showOpSuccess} text={labels.OpSavedGood} />

                <PageHeader
                    title={labels.TerritoriesDefenition}
                    text={labels.TerritoriesDefenitionExplanation}
                    icon={this.icon}
                    secondParagraph={labels.GuidedSetup_terittories_second_paragraph} />

                <TerritoriesTree
                    selectTerritoryHandler={this.__selectTerritory.bind(this)}
                    updatedTerritory={this.state.lastSaved}
                    afterSave={this.__handleFormSaved.bind(this)}
                    territories={this.state.territories} />

                {rightSide}
            </div>
        );

    }


    __showSuccess() {
        this.setState({showOpSuccess:true});
        window.setTimeout(() => this.setState({showOpSuccess:false}), 3000);
    }


    __selectTerritory(territory, tree) {

        setTimeout(() => {

            let selectedIds = tree.getSelectedId();

            if (selectedIds.length > 1) {

                let territories = this.state.territories.filter(t => selectedIds.indexOf(t.Id) > -1);

                this.setState({ selectedTerritory: territories });
            } else {

                for (let i=0; i<this.state.territories.length; i++) {
                    if (this.state.territories[i].Id === territory.id) {
                        territory.userdata = this.state.territories[i];
                        break;
                    }
                }

                this.setState({ selectedTerritory: territory });
            }

        }, 0);

    }

    __handleFormSaved(territory) {

        let territoriesToAdd = [],
            selectedTerritories = [];

        for (let i=0; i<territory.length; i++) {

            let found = false;

            for (let j=0; j<this.state.territories.length; j++) {

                if (this.state.territories[j].Id === territory[i].Id) {
                    this.state.territories[j] = territory[i];
                    found = true;
                }

            }

            if (!found) {
                territoriesToAdd.push(territory[i]);
            }

        }

        let combinedTerritories = territoriesToAdd.concat(this.state.territories);


        // reselect
        if (Array.isArray(this.state.selectedTerritory)) {

            for (let i=0; i<this.state.selectedTerritory.length; i++) {
                for (let j=0; j<combinedTerritories.length; j++) {

                    if (combinedTerritories[j].Id === this.state.selectedTerritory[i].Id) {
                        selectedTerritories.push(combinedTerritories[j]);
                    }

                }
            }
        }

        // single territory selected
        else if (this.state.selectedTerritory) {
            for (let i=0; i<combinedTerritories.length; i++) {
                if (this.state.selectedTerritory.Id === combinedTerritories[i].Id) {
                    selectedTerritories = combinedTerritories[i];
                    break;
                }
            }
        }



        this.setState(Object.assign(
                {},
                this.state, {lastSaved: territory},
                {territories: combinedTerritories, selectedTerritory: selectedTerritories}
            )
        );
    }

}

export default DefineTerritories;