import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';

export default class RulesExplanationLightbox extends Component {

    render() {
        return (
            <div className="lightbox-rules-explain">
                <h1>{labels.RulesTypes}</h1>
                <ul>
                    {this.formatRule(labels.RuleExp1)}
                    {this.formatRule(labels.RuleExp2)}
                    {this.formatRule(labels.RuleExp3)}
                    {this.formatRule(labels.RuleExp4)}
                    {this.formatRule(labels.RuleExp5)}
                    {this.formatRule(labels.RuleExp7)}
                    {this.formatRule(labels.RuleExp8)}
                    {this.formatRule(labels.RuleExp9)}
                    {this.formatRule(labels.RuleExp10)}
                    {this.formatRule(labels.RuleExp11)}
                    {this.formatRule(labels.RuleExp12)}
                    {this.formatRule(labels.RuleExp13)}
                </ul>
            </div>
        )
    }


    formatRule(rule) {
        return (<li><b>{rule.substr(0,rule.indexOf ('-')-1)}</b> {rule.substr(rule.indexOf ('-')-1)}</li>);
    }
}