import React, { Component } from 'react';
import OperatingHoursSelect from '../../../OperatingHoursSelect/OperatingHoursSelect';
import labels from '../../../../utils/customLabels';
import './styles.css';
import debounce from 'lodash/debounce';
import callRemoteAction from '../../../../utils/callRemoteAction';
import Lightbox from '../../../Lightbox/Lightbox';
import OperatingHoursCreator from '../../../OperatingHoursCreator/OperatingHoursCreator';
import { openSObjectLink } from '../../../../utils/Utils';
import Tooltip from "../../../Tooltip/Tooltip";


class TerritoryForm extends Component {

    constructor(props) {

        super(props);

        this.state = {
            territory: {
                Id: null,
                Name: null,
                OperatingHoursId: null,
                Address: {
                    street: null,
                    city: null,
                    state: null,
                    postalCode: null,
                    country: null,
                }
            },

            showWorkingHoursCreator: false,

            addNewOh: null

        };

        if (props.territory && props.territory.userdata) {
            for (let key in props.territory.userdata) {
                this.state.territory[key] = props.territory.userdata[key];
            }
        }

        this.nameBeforeSave = '';

        this.addressInputs = {};

        this.__saveChanges = debounce(this.__saveChanges, 2000);

    }


    render() {

        if (!this.props.territory) {
            return (
                <div className="territory-form">
                    <div className="select-a-territory">
                        {labels.NoTerritorySelectedText}
                    </div>
                </div>
            )
        }


        let googleMapsLink = null;
        if (this.state.territory.Longitude && this.state.territory.Latitude) {
            googleMapsLink = (<a className="open-external-link locate-in-gmap" href={'http://maps.google.com/?ll=' + this.state.territory.Latitude + ',' + this.state.territory.Longitude} target="_blank">{labels.LocateInGmap}</a>);
        }



        return (
            <div className="territory-form form-arrow">

                <div className="territory-form-header">
                    <h2 className="truncate">{this.nameBeforeSave}</h2>
                    <span className="truncate" onClick={this.__openSObjectLink.bind(this, this.state.territory.Id)}>{labels.Open_Service_Territory_Record}</span>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.OpHoursName}</div>
                    <div className="form-input">
                        <input maxlength="100" type="text" value={this.state.territory.Name} ref={ input => { this.territoryName = input }} onChange={this.__territoryNameChanged.bind(this)} />
                    </div>
                </div>

                <OperatingHoursSelect
                    multiselect={false}
                    addNewOh={this.state.addNewOh}
                    selected={this.state.territory.OperatingHoursId}
                    onChange={this.__operationHoursChanged.bind(this)} />

                <div className="create-operating-hours">

                    <div>
                        <span className="open-external-link" onClick={this.__openSObjectLink.bind(this, this.state.territory.OperatingHoursId)}>{labels.Open_Operating_Hours_Record}</span>
                    </div>

                    <div onClick={this.__openWorkingHoursCreator.bind(this)}>
                        {labels.CreateNewOH}
                    </div>
                </div>

                <Lightbox show={this.state.showWorkingHoursCreator} height="420px" width="680px" handleVisibility={this.__closeWorkingHoursCreator.bind(this)}>
                    <OperatingHoursCreator afterSave={this.__afterSaveOperatingHours.bind(this)} />
                </Lightbox>




                <div className="territory-address-mafrid">
                    <h2 className="form-territory-address">{labels.ObTerritoryAddress}</h2> 
                    <Tooltip text={labels.Territory_Address_Header_tooltip} />
                    {googleMapsLink}
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.ObAddress}</div>
                    <div className="form-input">
                        <input type="text" value={this.state.territory.Address.street} ref={ input => { this.addressInputs.street = input }} onChange={this.__addressFieldChanged.bind(this, 'street')} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.ObCity}</div>
                    <div className="form-input">
                        <input type="text" value={this.state.territory.Address.city} ref={ input => { this.addressInputs.city = input }} onChange={this.__addressFieldChanged.bind(this, 'city')} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.ObStateProvince}</div>
                    <div className="form-input">
                        <input type="text" value={this.state.territory.Address.state} ref={ input => { this.addressInputs.state = input }} onChange={this.__addressFieldChanged.bind(this, 'state')} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.ObZip}</div>
                    <div className="form-input">
                        <input type="text" value={this.state.territory.Address.postalCode} ref={ input => { this.addressInputs.postalCode = input }} onChange={this.__addressFieldChanged.bind(this, 'postalCode')} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.ObCountry}</div>
                    <div className="form-input">
                        <input type="text" value={this.state.territory.Address.country} ref={ input => { this.addressInputs.country = input }} onChange={this.__addressFieldChanged.bind(this, 'country')} />
                    </div>
                </div>


            </div>
        );
    }


    componentWillMount() {
        if (this.props && this.props.territory && this.props.territory.userdata) {
            this.nameBeforeSave = this.props.territory.userdata.Name;
        }
    }

    // will be called when a new territory was selected on the tree
    componentWillReceiveProps(nextProps) {

        if (nextProps.territory && nextProps.territory.userdata) {

            this.nameBeforeSave = nextProps.territory.userdata.Name;

            let t = {
                territory: nextProps.territory.userdata
            };

            if (t.territory.Address) {
                t.territory.Address.street = t.territory.Address.street || '';
                t.territory.Address.city = t.territory.Address.city || '';
                t.territory.Address.state = t.territory.Address.state || '';
                t.territory.Address.postalCode = t.territory.Address.postalCode || '';
                t.territory.Address.country = t.territory.Address.country || '';
            } else {
                t.territory.Address = {
                    street: '',
                    city: '',
                    state: '',
                    postalCode: '',
                    country: '',
                };
            }

            this.setState(Object.assign({}, this.state, t));
        }
    }


    __addressFieldChanged(field) {
        let territory = Object.assign({}, this.state.territory);
        territory.Address[field] = this.addressInputs[field].value;

        this.setState( Object.assign({}, this.state, {territory} ) );
        this.__saveChanges();
    }


    __territoryNameChanged() {
        let territory = Object.assign({}, this.state.territory);
        territory.Name = this.territoryName.value;

        this.setState( Object.assign({}, this.state, {territory} ) );
        this.__saveChanges();
    }

    __operationHoursChanged(operationHoursId) {

        let territory = Object.assign({}, this.state.territory);
        territory.OperatingHoursId = operationHoursId;

        this.setState( Object.assign({}, this.state, {territory} ));
        this.__saveChanges();
    }


    __saveChanges() {

        if (this.state.territory.Name === null) {
            return;
        }

        if (this.state.territory.Name === '') {
            alert(labels.TerritoryNameCantBeBlank);
            return;
        }

        // check if name already exist
        for (let i=0; i<this.props.territories.length; i++) {
            if (this.props.territories[i].Name.toUpperCase() === this.state.territory.Name.toUpperCase() && this.props.territories[i].Id !== this.state.territory.Id) {
                alert(labels.TerritoryNameExist);
                return;
            }
        }

        let territoryToSave = Object.assign({}, this.state.territory);
        delete territoryToSave['OperatingHours'];
        delete territoryToSave.Longitude;
        delete territoryToSave.Latitude;

        if (territoryToSave.Address) {
            Object.assign(territoryToSave, territoryToSave.Address);
            delete territoryToSave.Address;
        }

        callRemoteAction('updateServiceTerritories', JSON.stringify([territoryToSave]))
            .then(territories => {
                this.props.afterSave(territories);
            })
            .catch(ex => {
                console.error(ex);
            })
    }

    __openSObjectLink(id, ev) {
        ev.preventDefault();
        openSObjectLink(id);
    }


    __openWorkingHoursCreator() {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: true} ));
    }

    __closeWorkingHoursCreator() {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: false} ));
    }

    __afterSaveOperatingHours(oh) {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: false, addNewOh: oh } ));
        this.props.afterOpAdded();
    }
}

export default TerritoryForm;
