import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import labels from '../../../utils/customLabels';
import UserTerritoriesTable from "./UserTerritoriesTable/UserTerritoriesTable";
import lsdIcons from '../../../utils/lsdIcons';
import callRemoteAction from '../../../utils/callRemoteAction';
import ObjectNames from "../../../utils/ObjectNames";
import './styles.css'
import Lightbox from "../../Lightbox/Lightbox";



class SharingUsersTerritories extends Component {

    constructor() {
        super();

        this.state = {
            isNew: false,
            userTerritories: {},
            saving: false,
            lightboxUser: null,
            filter: '',
            filterTerritories: '',
            showAddLightbox: false,
            territories: [],
            selectedTerritories: {},
            assignedLicenses: {},
            agentPermissions: {},
            licensesIds: {},
            counters: {
                FieldServiceDispatcherPsl: {
                    total: 0,
                    used: 0
                }
            }
        };

        this.icon = {
            url: lsdIcons.socialshare,
            style: {
                background: '#21a972'
            }
        };

        this.maintext = (<div>{labels.SharingUsersTerritoriesExplanation}<ul><li>{labels.SharringMore1}</li><li>{labels.SharringMore2}</li></ul></div>);
        this.moreText = (<div>{labels.SharringMore3}</div>);


        this.getSUTdata();
    }

    render() {

        return (
            <div id="sharing-users-territORIs">
                <PageHeader title={labels.SharingUsersTerritories} text={this.maintext} icon={this.icon} more={this.moreText} />


                <div className="sut-search">
                    <input type="search" placeholder={labels.SearchUsersOb} value={this.state.filter} onChange={e => this.setState({filter: e.target.value})} />

                    <span className="sut-add guided-setup-button guided-setup-button_neutral" onClick={this.__addNewButton.bind(this)}>{labels.UTAddNew}</span>

                    <div className="sut-licenses" title={labels.RemainingDispatcherLicenses}>
                        <svg aria-hidden="true" className='slds-icon dispatcher-counter'>
                            <use href={lsdIcons.dispatcherLicense} />
                        </svg>
                        {labels.UTDispatchers} {this.state.counters.FieldServiceDispatcherPsl.used}\{this.state.counters.FieldServiceDispatcherPsl.total}
                    </div>
                </div>


                <Lightbox show={this.state.showAddLightbox} maxHeight="450px" width="680px" handleVisibility={this.__toggleAddNew.bind(this)}>
                    {this.__renderLightboxContent()}
                </Lightbox>


                <UserTerritoriesTable
                    userTerritories={this.state.userTerritories}
                    filter={this.state.filter}
                    editClicked={this.__toggleAddNew.bind(this)}
                    assignedLicenses={this.state.assignedLicenses}
                    licensesIds={this.state.licensesIds}
                    agentPermissions={this.state.agentPermissions}
                    users={this.state.users}
                    handleLicenseChanged={this.handleLicenseChanged.bind(this)}
                    />


            </div>
        );

    }


    __renderLightboxContent() {

        let saving = null;

        if (this.state.saving) {
            saving = (<div className="sut-saving">
                <img src={lsdIcons.spinner} />
                {labels.SavingUserTerritories}
            </div>)
        }



        if (this.state.showAddLightbox) {
            return (
                <div className="sut-territories-container">

                    <div>
                        {this.__generateLightboxTitle()}
                        <input
                            type="search"
                            placeholder={labels.SearchTerrriris}
                            onChange={e => this.setState({filterTerritories: e.target.value})} />

                        {this.__generateLightboxIntro()}
                    </div>

                    <div id="sut-ter-checkbox">{this.__generateTerritoriesCheckbox(this.state.lightboxUser)}</div>

                    {saving}
                    <div id="cut-button" className="guided-setup-button guided-setup-button_brand" onClick={this.__createUserTerritories.bind(this)}>{labels.CreateUserTerritories}</div>
                </div>
            )
        } else {
            return null;
        }

    }


    __generateLightboxTitle() {

        if (this.state.isNew) {

            let usersWithNoUt = this.state.users
                .filter(u => !this.state.userTerritories[u.Id] || this.state.userTerritories[u.Id].length === 0)
                .sort( (a,b) => a.Name.toUpperCase() > b.Name.toUpperCase() ? 1 : -1)
                .map(u => (<option value={u.Id}>{u.Name}</option>));

            usersWithNoUt.unshift(<option value={null}>{labels.CUTSelectUser}</option>)

            return (<h1>
                {labels.AssignTerritoriesFor}
                <select id="sut-select"
                        value={this.state.lightboxUser}
                        onChange={e => this.setState({lightboxUser: e.target.value})}>
                            {usersWithNoUt}
                    </select>
            </h1>);

        } else {
            return (<h1>{labels.AssignTerritoriesFor} {this.state.userTerritories[this.state.lightboxUser][0].userName}</h1>);
        }

    }

    __generateLightboxIntro() {
        return (
            <h2>
                {labels.Assign_Territories_Dispatcher_intro1}<br/>
                <a href="https://help.salesforce.com/articleView?id=fs_sharing.htm" target="_blank">{labels.Assign_Territories_Dispatcher_intro2}</a>
            </h2>
        );
    }


    __createUserTerritories() {

        if (!this.state.lightboxUser) {
            alert(labels.NoUSelected);
            return;
        }


        if (this.state.isNew) {
            let somethingSelected = false;
            for (let key in this.state.selectedTerritories) {
                if (this.state.selectedTerritories[key]) {
                    somethingSelected = true;
                    break;
                }
            }

            if (!somethingSelected) {
                alert(labels.Nocurrentterr47);
                return;
            }
        }



        let territoriesIds = [];

        for (let tId in this.state.selectedTerritories) {
            if (this.state.selectedTerritories[tId]) {
                territoriesIds.push(tId);
            }
        }

        this.setState({saving: true});

        callRemoteAction('updateUserTerritories', this.state.lightboxUser, territoriesIds).then(newUts => {

            let userId = this.state.lightboxUser,
                userTerritories = Object.assign({}, this.state.userTerritories),
                userTerritoriesArr = [];

            newUts.forEach(ut => {
                userTerritoriesArr.push(this.parseUserTerritory(ut));
            });

            if (userTerritoriesArr.length > 0) {
                userTerritories[userId] = userTerritoriesArr;
            } else {
                delete userTerritories[userId];
            }


            this.setState({userTerritories, saving: false, lightboxUser:null, showAddLightbox: false});

        }).catch(ex => {
            console.error(ex);
            this.setState({ saving: false});
        })


    }



    __addNewButton() {
        this.__toggleAddNew();
    }

    __toggleAddNew(userId) {

        let selectedTerritories = {};

        if (userId && this.state.userTerritories[userId]) {
            this.state.userTerritories[userId].forEach(ut => selectedTerritories[ut.territoryId] = true);
        }


        this.setState(
            {
                isNew: !userId,
                filterTerritories: '',
                showAddLightbox: !this.state.showAddLightbox,
                selectedTerritories,
                lightboxUser: userId
            }
        );
    }


    __generateTerritoriesCheckbox() {

        return this.state.territories
            .filter(t => t.Name.toUpperCase().indexOf(this.state.filterTerritories.toUpperCase()) > -1)
            .sort( (a,b) => a.Name.toUpperCase() > b.Name.toUpperCase() ? 1 : -1)
            .map(t => {


                return (
                    <span className="sut-ter-lab-cont">
                        <input
                            id={'territory_' + t.Id}
                            type="checkbox"
                            checked={this.state.selectedTerritories[t.Id]}
                            onClick={this.checkTerritoryInLightbox.bind(this, t.Id)} />

                        <label htmlFor={'territory_' + t.Id}>{t.Name}</label>
                    </span>
                );


        });

    }


    checkTerritoryInLightbox(tid,e) {

        let selectedTerritories = Object.assign({}, this.state.selectedTerritories);

        selectedTerritories[tid] = e.target.checked;

        this.setState({selectedTerritories});

    }


    checkIfTerritorySelected(userId, tId) {

        if (!this.state.userTerritories[userId]) {
            return false;
        }

        let ut = this.state.userTerritories[userId];

        for (let i=0; i<ut.length; i++) {
            if (ut[i].territoryId === tId) {
                return true;
            }
        }

        return false;

    }


    getSUTdata() {

        callRemoteAction('getSUTdata').then(result => {

            let counters = {},
                assignedLicenses = {},
                licensesIds = {},
                agentPermissions = {},
                userTerritories = {};


            // LICENSES (FieldServiceDispatcherPsl
            result.licensesIds.forEach(l => {
                licensesIds[l.DeveloperName] = l.Id;
            });


            // ASSIGNED PERMISSION FOR AGENT PERMISSION BLAT
            result.assignedPermissionSet.forEach(aps => {
                agentPermissions[aps.AssigneeId] = true;
            });

            // ASSIGNED LICENSES
            result.assignedLicenses.forEach(al => {
                assignedLicenses[al.AssigneeId] = assignedLicenses[al.AssigneeId] || [];
                assignedLicenses[al.AssigneeId].push(al);
            });


            // LICENSES
            result.licensesCounter.forEach(r => {
                counters[r.DeveloperName] = {
                    total: r.TotalLicenses,
                    used: r.UsedLicenses
                }
            });


            // USER TERRITORIES
            result.userTerritories.forEach(ut => {

                let parsedUt = this.parseUserTerritory(ut);

                userTerritories[parsedUt.userId] = userTerritories[parsedUt.userId] || [];
                userTerritories[parsedUt.userId].push(parsedUt);
            });


            this.setState({
                assignedLicenses,
                licensesIds,
                userTerritories,
                counters,
                agentPermissions,
                users: result.users,
                territories: result.territories
            });
        })

    }


    parseUserTerritory(ut) {

        return {
            Id: ut.Id,
            territoryId: ut[ObjectNames.UserTerritories.ServiceTerritory],
            territoryName: ut[ObjectNames.UserTerritories.TerritoryName],
            userId: ut[ObjectNames.UserTerritories.User.replace('__c','__r')].Id,
            userName: ut[ObjectNames.UserTerritories.User.replace('__c','__r')].Name,
        }
    }


    handleLicenseChanged(userId, action, isDispatcher, cb) {

        callRemoteAction('removeAgentDispatcherPermissionSets', userId, action, isDispatcher).then(result => {

            if (action === 'add' || (action === 'remove' && !isDispatcher) ) {

                this.__handleReplyWhenLicenseChanged(userId, cb, result);

            } else if (isDispatcher && action === 'remove') {

                callRemoteAction('removeDispatcherLicense', userId).then(result => {
                    this.__handleReplyWhenLicenseChanged(userId, cb, result);
                })

            }


        }).catch(ex => {
            window.alert(ex.message);
            cb();
        });

    }


    __handleReplyWhenLicenseChanged(userId, cb, result) {

        let counters = {},
            assignedLicenses = Object.assign({}, this.state.assignedLicenses),
            agentPermissions = Object.assign({}, this.state.agentPermissions);


        // ASSIGNED LICENSES
        assignedLicenses[userId] = [];
        result.assignedLicenses.forEach(al => {
            assignedLicenses[al.AssigneeId].push(al);
        });


        result.licensesCounter.forEach(r => {
            counters[r.DeveloperName] = {
                total: r.TotalLicenses,
                used: r.UsedLicenses
            }
        });


        if (result.assignedPermissionSet.length === 0) {
            agentPermissions[userId] = false;
        } else {
            result.assignedPermissionSet.forEach(aps => {
                agentPermissions[aps.AssigneeId] = true;
            });
        }


        let deleteIds = [];
        for (let al in assignedLicenses) {
            if (assignedLicenses[al].length === 0) {
                deleteIds.push(al)
            }
        }

        deleteIds.forEach(id => delete assignedLicenses[id]);


        cb();

        this.setState({
            assignedLicenses,
            agentPermissions,
            counters
        });
    }


}

export default SharingUsersTerritories;