import callRemoteAction from '../utils/callRemoteAction';
import { decodeHTML } from '../utils/Utils';
import ObjectNames from '../utils/ObjectNames';

class Policies {

    constructor() {
        this.synced = false;

    }

    get() {

        if (this.synced) {
            return new Promise( resolve => resolve(this.policies) );
        }


        return new Promise( (resolve, reject) => {

            callRemoteAction('getSchedulingPolicies').then(policies => {

                this.parseAllPoliciesObjects(policies);
                this.synced = true;
                resolve(this.policies);

            }).catch(ex => {
                this.synced = false;
                console.error('Unable to get operating hours');
                reject(ex);
            });

        });
    }


    parseAllPoliciesObjects(policies) {

        policies.goalsJunction = policies.goalsJunction.map(gj => {
            return {
                Id: gj.Id,
                SchedulingPolicy: gj[ObjectNames.SchedulingPolicyGoal.SchedulingPolicy],
                ServiceGoal: gj[ObjectNames.SchedulingPolicyGoal.Service_Goal],
                Weight: gj[ObjectNames.SchedulingPolicyGoal.Weight]
            }
        });

        policies.rulesJunction = policies.rulesJunction.map(rj => {
            return {
                Id: rj.Id,
                SchedulingPolicy: rj[ObjectNames.SchedulingPolicyWorkRule.SchedulingPolicy],
                WorkRule: rj[ObjectNames.SchedulingPolicyWorkRule.WorkRule]
            }
        });

        policies.policies = policies.policies.map(this.parsePolicy).sort( (a,b) => a.Name <= b.Name ? -1 : 1);

        this.policies = policies;

    }


    getNow() {
        return this.policies;
    }

    upsertPolicy(policy) {

        for (let i=0; i<this.policies.policies.length; i++) {
            if (this.policies.policies[i].Id === policy.Id) {
                this.policies.policies[i] = policy;
                return;
            }
        }

        // new policy
        this.policies.policies.push(policy);
    }

    parsePolicy(policy) {
        return {
            Id: policy.Id,
            Name: decodeHTML(policy.Name),
            Description: decodeHTML(policy[ObjectNames.SchedulingPolicy.Description]) || '',
            Daily_Optimization__c: policy[ObjectNames.SchedulingPolicy.Daily_Optimization__c] || false,
            Commit_Mode__c: policy[ObjectNames.SchedulingPolicy.Commit_Mode__c] || 'AlwaysCommit',
            Fix_Overlaps__c: policy[ObjectNames.SchedulingPolicy.Fix_Overlaps__c] || false
        };
    }

    parseRulePolicyJunction(junction) {
        return {
            Id: junction.Id,
            SchedulingPolicy: junction[ObjectNames.SchedulingPolicyWorkRule.SchedulingPolicy],
            WorkRule: junction[ObjectNames.SchedulingPolicyWorkRule.WorkRule]
        };
    }


    parsePolicyObjective(junction) {
        return {
            Id: junction.Id,
            SchedulingPolicy: junction[ObjectNames.SchedulingPolicyGoal.SchedulingPolicy],
            ServiceGoal: junction[ObjectNames.SchedulingPolicyGoal.Service_Goal],
            Weight: junction[ObjectNames.SchedulingPolicyGoal.Weight]
        }
    }

    upsertPolicyObjectiveJunction(junction) {

        let parsedJunction = this.parsePolicyObjective(junction),
            goalsJunction = this.policies.goalsJunction;

        for (let i=0; i<goalsJunction.length; i++) {
            if (goalsJunction[i].Id === parsedJunction.Id) {
                goalsJunction[i] = parsedJunction;
                return;
            }
        }

        // new policy
        goalsJunction.push(parsedJunction);

    }


    deletePolicyObjectiveJuction(id) {

        let goalsJunction = this.policies.goalsJunction;

        for (let i=0; i<goalsJunction.length; i++) {
            if (goalsJunction[i].Id === id) {
                goalsJunction.splice(i,1);
                return;
            }
        }
    }

    updatePolicies(field, obj) {
        policies.policies[field] = obj;
    }

}

let policies = new Policies();

export default policies;