import React, { Component } from 'react';
import './styles.css';
// import labels from '../../utils/customLabels';



class TimeSelect extends Component {


    constructor(props) {
        super(props);

        this.state = {
            hours: props.hours || 8,
            minutes: props.minutes || 0,
            ampm: props.ampm ? props.ampm : null
        }
    }


    render() {

        let hoursOptions = [],
            minutesOptions = [],
            hours = this.props.ampm ? 12 : 24;


        for (let i=0; i<hours; i++) {
            hoursOptions.push(<option value={i}>{displayTwoDigits(i)}</option>);
        }


        for (let i=0; i<60; i+=10) {
            minutesOptions.push(<option value={i}>{displayTwoDigits(i)}</option>);
        }


        let ampmSelect = !this.props.ampm ? null : (
            <div className="time-container-thingy minutes-select">
                <select value={this.state.ampm}
                        onChange={this.__onTimeChanged.bind(this)}
                        ref={ input => { this.selectAmpm = input }}>
                    <option value="AM">AM</option>
                    <option value="PM">PM</option>
                </select>
            </div>
        );


        return (

            <span>
                <div className="time-container-thingy">
                    <select
                        value={this.state.hours}
                        onChange={this.__onTimeChanged.bind(this)}
                        ref={ input => { this.selectHours = input }}>
                            {hoursOptions}
                    </select>
                </div>
                :
                <div className="time-container-thingy minutes-select">
                    <select
                        value={this.state.minutes}
                        onChange={this.__onTimeChanged.bind(this)}
                        ref={ input => { this.selectMinutes = input }}>
                            {minutesOptions}
                    </select>
                </div>
                {ampmSelect}
            </span>
        );

    }

    componentWillUpdate(nextProps, nextState) {
        if  (JSON.stringify(nextState) !== JSON.stringify(this.state)) {
            this.props.onChange(nextState);
        }

    }


    __onTimeChanged() {

        this.setState({
            hours: this.selectHours.value,
            minutes: this.selectMinutes.value,
            ampm: this.selectAmpm ? this.selectAmpm.value : null,
        });
    }

}

function displayTwoDigits(n) {
    if (n < 10) {
        return '0' + n;
    }

    if (n === 24) {
        return '00';
    }

    return n;
}


export default TimeSelect;