import React, { Component } from 'react';
import operatingHours from '../../data/OperatingHours';
import labels from '../../utils/customLabels';
import {timezoneList} from '../../utils/miscSalesforceObjects';
import Tooltip from "../Tooltip/Tooltip";

import './styles.css';

class OperatingHoursSelect extends Component {

    constructor(props) {
        super(props);
        this.state = { operatingHours: [] , defaultHours: props.selected};
    }


    render() {
        return (
            <div className="oh-form">
                <div className="ohs-label">{labels.OperatingHoursLabel}
                    <Tooltip text={labels.GuidedSetup_appt_booking_oh_tooltip} />
                </div>
                <div className="ohs-input">
                    <select value={this.state.defaultHours}
                            onChange={this.__onTerritoryChanged.bind(this)}
                            ref={ input => { this.selectedOperatingHours = input }} >
                                {this.__generateOptions()}
                    </select>
                </div>
            </div>
        );
    }


    // will be called when new territory is selected
    componentWillReceiveProps(nextProps) {

        this.setState(Object.assign({}, this.state, { defaultHours: nextProps.selected } ));

        // save with default OH if new
        // if (!this.props.selected && this.state.operatingHours.length > 0) {
        //     this.props.onChange(this.state.operatingHours[0].Id);
        // }
    }


    componentDidMount() {

        operatingHours.get().then(oh => {
            this.setState({ operatingHours: oh , defaultHours: this.props.selected || oh[0].Id });

            // save with default OH if new
            if (!this.props.selected && this.state.operatingHours.length > 0) {
                this.props.onChange(this.state.operatingHours[0].Id);
            }

        });

    }


    componentWillUpdate(nextProps) {

        if (JSON.stringify(this.props.addNewOh) !== JSON.stringify(nextProps.addNewOh)) {
            operatingHours.addNewOh(nextProps.addNewOh);
            this.setState(Object.assign({}, this.state, { operatingHours: operatingHours.getNow() } ));
        }



    }



    __generateOptions() {

        let options = this.props.multiselect ? [<option value='none'>{labels.SelectOperatingHours}</option>] : [];

        this.state.operatingHours
            .sort( (a,b) => a.Name.toUpperCase() <= b.Name.toUpperCase() ? -1 : 1)
            .forEach(oh => {

                if (this.props.dontShowTz) {
                    options.push(<option value={oh.Id}>{oh.Name}</option>);
                } else {
                    options.push(<option value={oh.Id}>{oh.Name} {OperatingHoursSelect.__findTzOptionLabel(oh.TimeZone)}</option>);
                }


        });

        return options;

    }


    static __findTzOptionLabel(tz) {

        for (let i=0; i<timezoneList.length; i++) {
            if (timezoneList[i].value === tz) {
                return timezoneList[i].label.substring(0,11);
            }
        }

    }


    __onTerritoryChanged() {

        if (this.props.onChange) {
            this.props.onChange(this.selectedOperatingHours.value);
        }
    }

}


export default OperatingHoursSelect;