import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css'
import { openSObjectLink } from '../../../../utils/Utils';
import Tooltip from "../../../Tooltip/Tooltip";


class PolicyForm extends Component {

    constructor(props) {
        super(props);

        this.state = {
            nameBeforeSave: (props.selected && props.selected.Name) || null,
            policy: props.selected || null,
            isUseEdge: window.inday_useEdge
        };

        this.inputs = {};

        this.commitModeTooltipText = (<div>{labels.guidedsetup_commit_mode_tooltip_a}
                <ul className="rd-comments-ul">
                    <li>{labels.guidedsetup_commit_mode_tooltip_b}</li>
                    <li>{labels.guidedsetup_commit_mode_tooltip_c}</li>
                </ul>
                {labels.guidedsetup_commit_mode_tooltip_d}
            </div>);

        let indayAlert = this.state.isUseEdge ? '' : labels.guidedsetup_inday_tooltip_disabled;
        this.indayTooltipText = (<div>
                <span className="inday-disabled">{indayAlert}</span>
                
                {labels.guidedsetup_inday_tooltip}
            </div>);
    }


    render() {

        return (

            <div>

                <h1 className="g_obh_form_title">
                    {this.state.nameBeforeSave}
                    <span className="policy-link-layout" onClick={this.__openSObjectLink.bind(this, this.state.policy.Id)}>{labels.Open_Scheduling_Policy_Record}</span>
                </h1>

                <div className="form-row-container">
                    <div className="form-label">{labels.PolicyName}
                        <Tooltip text={labels.SchedulingPolicy_form_name_tooltip} />
                    </div>
                    <div className="form-input">
                        <input
                            maxlength="100"
                            type="text"
                            value={this.state.policy.Name}
                            onBlur={this.__nameOnBlur.bind(this)}
                            onChange={this.__fieldChanged.bind(this, 'Name', false)} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.PolicyDescription}</div>
                    <div className="form-input">
                        <textarea
                            value={this.state.policy.Description}
                            onChange={this.__fieldChanged.bind(this, 'Description', false)} />
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.In_Day}
                        <Tooltip text={this.indayTooltipText} />
                    </div>
                    <div className="form-input checkbox_toggle">
                        <input type="checkbox" disabled={!this.state.isUseEdge} checked={this.state.policy.Daily_Optimization__c}
                               onChange={this.__fieldChanged.bind(this, 'Daily_Optimization__c', true)}/>
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.Commit_mode}
                        <Tooltip text={this.commitModeTooltipText} />
                    </div>
                    <div className="form-input form-select">
                        <select value={this.state.policy.Commit_Mode__c}
                                onChange={this.__fieldChanged.bind(this, 'Commit_Mode__c', false)} >
                            <option value="AlwaysCommit">Always Commit</option>
                            <option value="Rollback">Rollback</option>
                        </select>
                    </div>
                </div>


                <div className="form-row-container">
                    <div className="form-label">{labels.FixOverlaps}
                        <Tooltip text={labels.guidedsetup_fixoverlaps_tooltip} />
                    </div>
                    <div className="form-input checkbox_toggle">
                        <input type="checkbox" checked={this.state.policy.Fix_Overlaps__c}
                               onChange={this.__fieldChanged.bind(this, 'Fix_Overlaps__c', true)}/>
                    </div>
                </div>

            </div>

        );

    }


    componentWillReceiveProps(nextProps) {

        if ((this.state.policy.Description === nextProps.selected.Description && this.state.policy.Name === nextProps.selected.Name)
            || (this.state.policy.Id !== nextProps.selected.Id)) {
                this.setState( {
                    nameBeforeSave: nextProps.selected.Name,
                    policy: nextProps.selected
                });
        }
    }

    __nameOnBlur() {
        let policy = Object.assign({}, this.state.policy);
        //policy.Name = this.state.nameBeforeSave;
        this.setState({policy});
    }

    __fieldChanged(field, isCheckbox = false, event) {

        let policy = Object.assign({}, this.state.policy);
        policy[field] = isCheckbox ? event.target.checked : event.target.value;
        this.setState({policy});

        this.props.formSavedCallback(policy.Id, policy.Name, policy.Description, policy.Daily_Optimization__c, policy.Commit_Mode__c, policy.Fix_Overlaps__c);

    }

    __openSObjectLink(id, ev) {
        ev.preventDefault();
        openSObjectLink(id);
    }



}

export default PolicyForm;