import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import labels from '../../../Utils/customLabels';
import './styles.css';

class Navigation extends Component {

    constructor() {
        super();
    }

    render() {



        return (
            <div className="link-box">
                <Link className={this.getCssClass('/')} to="/">{labels.OBHome}</Link>
                <Link className={this.getCssClass('DefineTerritories')} to="/DefineTerritories">1. {labels.TerritoriesDefenition}</Link>
                <Link className={this.getCssClass('WorkTypesAndSkills')} to="/WorkTypesAndSkills">2. {labels.WorkTypesAndSkills}</Link>
                <Link className={this.getCssClass('ResourcesDefenition')} to="/ResourcesDefenition">3. {labels.ResourcesDefenition}</Link>
                <Link className={this.getCssClass('SharingUsersTerritories')} to="/SharingUsersTerritories">4. {labels.SharingUsersTerritories}</Link>
                <Link className={this.getCssClass('AppointmentBooking')} to="/AppointmentBooking">5. {labels.AppointmentBooking}</Link>
                <Link className={this.getCssClass('SchedulingPolicies')} to="/SchedulingPolicies">6. {labels.SchedulingPolicies}</Link>

            </div>
        );
    }


    getCssClass(name) {

        if (name === '/') {
            if (window.location.href.substr(window.location.href.length-2, 2) === '#/') {
                return 'menu-link obnav-active';
            } else {
                return 'menu-link';
            }
        }

        return window.location.href.indexOf(name) > -1 ? 'menu-link obnav-active' : 'menu-link'
    }
}

export default Navigation;
