import React, { Component } from 'react';
import PageHeader from '../../PageHeader/PageHeader';
import labels from '../../../utils/customLabels';
import callRemoteAction from '../../../utils/callRemoteAction';
import ResourcesTable from "./ResourcesTable/ResourcesTable";
import ObjectNames from "../../../utils/ObjectNames";
import SearchResources from "./SearchResources/SearchResources";
import lsdIcons from '../../../utils/lsdIcons';
import LicensesCounter from "./LicensesCounter/LicensesCounter";
import debounce from 'lodash/debounce';
import AddNewResource from "./AddNewResource/AddNewResource";
import './styles.css';
import ResourcesBulkActions from "./ResourcesBulkActions/ResourcesBulkActions";

class ResourcesDefenition extends Component {

    constructor() {
        super();

        this.state = {
            searchText: '',
            selectedResourcesIds: [],
            resources: {},
            users: [],
            licenseTypes: {},
            assignedLicenses: {},
            permissionSets: {},
            territories: [],
            territoryMembers: {},
            selectedResourcesIds: [],
            licensesCounters: {
                FieldServiceMobilePsl: {total: 0, used: 0},
                FieldServiceSchedulingPsl: {total: 0, used: 0}
            }
        };

        this.icon = {
            url: lsdIcons.resources,
            style: {
                background: '#ef6e64'
            }
        };

        let fslClasses = 'slds-icon open-icon';

        //this.moreText = (<div><ul>{labels.ResourceMore1}</ul><ul>{labels.ResourceMore2}</ul><ul>{labels.ResourceMore3}</ul></div>)
        let secondParagraph = labels.ResourcesDefenitionExplanation2.split('$0');
        this.secondParagraph = (<div>{secondParagraph[0]} <svg aria-hidden="true" className={fslClasses}> <use href={lsdIcons.open} /> </svg> {secondParagraph[1]}</div>);


        this.saveResource = debounce(this.saveResource, 2000);


        this.getResourcesAndUsers();


        callRemoteAction('getServiceTerritories').then(territories => {

            let sortedTerritories =  territories.sort( (a,b) => a.Name <= b.Name ? 1 : -1);
            this.setState({
                territories : sortedTerritories
            });


        }).catch(ex => {
            console.error(ex);
        });



    }

    render() {

        return (
            <div id="rd-container">
                <PageHeader
                    title={labels.ResourcesDefenition}
                    text={labels.ResourcesDefenitionExplanation}
                    icon={this.icon}
                    secondParagraph={this.secondParagraph} />

                <AddNewResource
                    resources={this.state.resources}
                    users={this.state.users}
                    licenseTypes={this.state.licenseTypes}
                    assignedLicenses={this.state.assignedLicenses}
                    onAdded={this.__onResourceCreated.bind(this)}
                    territories={this.state.territories} />

                <SearchResources onChange={this.__searchTermChanged.bind(this)}/>

                <ResourcesBulkActions selectedIds={this.state.selectedResourcesIds}/>

                <LicensesCounter counters={this.state.licensesCounters} />

                <ResourcesTable
                    search={this.state.searchText}
                    resources={this.state.resources}
                    users={this.state.users}
                    licenseTypes={this.state.licenseTypes}
                    assignedLicenses={this.state.assignedLicenses}
                    territories={this.state.territories}
                    territoryMembers={this.state.territoryMembers}
                    saveResource={this.saveResource.bind(this)}
                    licenseChanged={this.licenseChanged.bind(this)}
                    onRowSelected={this.selectedRowsUpdated.bind(this)} />

            </div>
        );

    }


    selectedRowsUpdated(selectedResourcesIds) {
        this.setState({selectedResourcesIds});
    }

    __onResourceCreated(resource, licenses, cb) {

        let permissionSetsIds = [];

        if (licenses.fsl) {
            permissionSetsIds.push(...this.state.permissionSets.fsl);
        }

        if (licenses.mobile) {
            permissionSetsIds.push(...this.state.permissionSets.mobile);
        }


        if (licenses.mobile) {

            if (this.state.licensesCounters.FieldServiceMobilePsl.used === this.state.licensesCounters.FieldServiceMobilePsl.total) {
                alert(labels.OutOfMobileLicenses);
                cb(true);
                return;
            }

        } else if (licenses.fsl) {

            if (this.state.licensesCounters.FieldServiceSchedulingPsl.used === this.state.licensesCounters.FieldServiceSchedulingPsl.total) {
                alert(labels.OutOfFslLicenses);
                cb(true);
                return;
            }

        }



        callRemoteAction('createPermissionSets', resource.user, permissionSetsIds).then(permissionsResult => {

            if (permissionsResult.permissions) {

                callRemoteAction('createNewResource', resource).then(result => {

                    let resources = Object.assign({}, this.state.resources),
                        territoryMembers = Object.assign({}, this.state.territoryMembers),
                        assignedLicenses = Object.assign({}, this.state.assignedLicenses),
                        licensesCounters = {};


                    // assigned license
                    result.assignedLicenses.forEach(al => {
                        assignedLicenses[al.AssigneeId] = assignedLicenses[al.AssigneeId] || {};
                        assignedLicenses[al.AssigneeId][al.PermissionSetLicenseId] = al.Id;
                    });


                    // parse resource
                    resources[resource.user] = ResourcesDefenition.parseResource(result.resource);

                    // parse stm
                    territoryMembers[result.stm.ServiceResourceId] = { name: result.stm.ServiceTerritory.Name || '', id: result.stm.ServiceTerritory.Id || null };

                    // set license counters
                    result.licenseCounter.forEach(c => licensesCounters[c.DeveloperName] = { total: c.TotalLicenses , used: c.UsedLicenses});




                    this.setState({resources, territoryMembers, assignedLicenses, licensesCounters});

                    cb();

                }).catch(ex => {
                    console.error((ex))
                    alert(ex.message);
                    cb(true);
                });

            } else {
                cb(true);
            }

        }).catch(ex => {
            console.error((ex));
            alert(ex.message);
            cb(true);
        })

    }



    __searchTermChanged(text) {
        this.setState({searchText: text});
    }


    getResourcesAndUsers() {

        callRemoteAction('getResourcesAndUsers').then(result => {

            let resources = {},
                licenseTypes = {},
                assignedLicenses = {},
                territoryMembers = {},
                licensesCounters = {},
                permissionSets = {
                    mobile: [],
                    fsl: []
                };


            // set license counters
            result.licenseCounter.forEach(c => licensesCounters[c.DeveloperName] = { total: c.TotalLicenses , used: c.UsedLicenses});

            this.setState({assignedLicenses, licensesCounters});


            result.territoryMembers.forEach(stm => territoryMembers[stm.ServiceResourceId] = { name: stm.ServiceTerritory.Name || '', id: stm.ServiceTerritory.Id || null } );

            // licenses - should have 2 - FieldServiceMobilePsl && FieldServiceSchedulingPsl
            result.licenseTypes.forEach(type => {
                licenseTypes[type.DeveloperName] = type.Id;
            });

            // { userId , {licenseId, assignmentId} }
            result.assignedLicenses.forEach(al => {
                assignedLicenses[al.AssigneeId] = assignedLicenses[al.AssigneeId] || {};
                assignedLicenses[al.AssigneeId][al.PermissionSetLicenseId] = al.Id;
            });


            // permission sets
            result.permissionSets.forEach(ps => {
                if (ps.Name === 'FSL_Mobile_License') {
                    permissionSets.mobile.push(ps.Id)
                } else {
                    permissionSets.fsl.push(ps.Id)
                }
            });

            result.resources.forEach(r => {
                resources[r.RelatedRecordId] = ResourcesDefenition.parseResource(r);
            });

            this.setState({
                resources,
                users: result.users,
                licenseTypes,
                territoryMembers,
                assignedLicenses,
                permissionSets,
                licensesCounters
            })
        });

    }


    static parseResource(sObject) {

        let resource = Object.assign({} , sObject);

        resource.GanttLabel = sObject[ObjectNames.ServiceResource.GanttLabel] || '';
        resource.Name = sObject.Name || '';
        delete resource[ObjectNames.ServiceResource.GanttLabel];

        return resource;
    }


    saveResource(value, field, resourceId) {

        let updateField = field;

        switch (field) {
            case 'resourceName': updateField = 'Name'; break;
            case 'GanttLabel': updateField = ObjectNames.ServiceResource.GanttLabel; break;
        }

        let resource = { Id: resourceId };
        resource[updateField] = value;

        callRemoteAction('updateResource', resourceId, JSON.stringify(resource)).then(updatedResource => {

            let newResources = Object.assign({}, this.state.resources),
                parsedResource = ResourcesDefenition.parseResource(updatedResource);

            newResources[parsedResource.RelatedRecordId] = parsedResource;

            this.setState({resources: newResources});
        })
    }


    changeResourceActive(userId, value) {
        return callRemoteAction('changeResourceActive', userId, value)
    }

    changeResourceIncludeInOptimization(userId, value) {
        return callRemoteAction('changeOptimizationCapablity', userId, value)
    }



    async licenseChanged(userId, type, isAdded, cb) {

        let isFirstPermission = false,
            licensesIds = {
            fsl: this.state.licenseTypes['FieldServiceSchedulingPsl'],
            mobile: this.state.licenseTypes['FieldServiceMobilePsl']
        };


        // adding first permission set
        if (isAdded && (this.state.assignedLicenses[userId] && Object.keys(this.state.assignedLicenses[userId]).length === 0) || (this.state.assignedLicenses[userId] === undefined)) {
            isFirstPermission = true;
        }

        // removing the only permission set we have
        else if ((this.state.assignedLicenses[userId] && Object.keys(this.state.assignedLicenses[userId]).length === 1)  && (!isAdded))  {

            let resource = await this.changeResourceActive(userId, false);
            let resources = Object.assign({}, this.state.resources);
            resources[userId].IsActive = false;
            this.setState({resources});
        }

        // remove fsl license
        if (!isAdded) {

            if (type !== 'mobile') {
                let resource = await this.changeResourceIncludeInOptimization(userId, false);
            }

            callRemoteAction('removePermissionSets', userId, type === 'mobile').then(counters => {

                let assignedLicenses = Object.assign({}, this.state.assignedLicenses);
                delete assignedLicenses[userId][licensesIds[type]];

                let licensesCounters = {};
                counters.forEach(c => licensesCounters[c.DeveloperName] = { total: c.TotalLicenses , used: c.UsedLicenses});

                this.setState({assignedLicenses, licensesCounters});
                cb();

            });
        }

        else if (isAdded) {


            if (type === 'mobile') {

                if (this.state.licensesCounters.FieldServiceMobilePsl.used === this.state.licensesCounters.FieldServiceMobilePsl.total) {
                    alert(labels.OutOfMobileLicenses);
                    cb();
                    return;
                }

            } else {

                if (this.state.licensesCounters.FieldServiceSchedulingPsl.used === this.state.licensesCounters.FieldServiceSchedulingPsl.total) {
                    alert(labels.OutOfFslLicenses);
                    cb();
                    return;
                }

            }


            callRemoteAction('addPermissionSets', userId, this.state.permissionSets[type]).then(async result => {

                let assignedLicenses = Object.assign({}, this.state.assignedLicenses);

                let counters = result.licenseCounter,
                    licensesCounters = {};


                // { userId , {licenseId, assignmentId} }
                result.permissions.forEach(al => {
                    assignedLicenses[al.AssigneeId] = assignedLicenses[al.AssigneeId] || {};
                    assignedLicenses[al.AssigneeId][al.PermissionSetLicenseId] = al.Id;
                });

                counters.forEach(c => licensesCounters[c.DeveloperName] = { total: c.TotalLicenses , used: c.UsedLicenses});

                // first permission added, set IsActive = true
                if (isFirstPermission) {
                    let resourceIsFirstPermission = await this.changeResourceActive(userId, true);

                    let resources = Object.assign({}, this.state.resources);
                    resources[userId].IsActive = true;
                    this.setState({resources});

                }


                if (type !== 'mobile') {
                    let resource = await this.changeResourceIncludeInOptimization(userId, true);
                }



                this.setState({assignedLicenses, licensesCounters});

                cb();


            }).catch(ex => {
                window.alert(ex.message);
                cb();
            });


        }

    }

}

export default ResourcesDefenition;