import React, { Component } from 'react';
import './styles.css';
import labels from '../../../../utils/customLabels';


export default class AddPolicy extends Component {

    constructor(props) {
        super(props);

        this.saving = false;

    }


    render() {

        return (
            <div id="AddNewPolicy" className="form-input">
                <input type="text"
                       maxlength="100"
                       placeholder={labels.PolicyNamePlaceHolder}
                       ref={i => this.policyNameInput = i}
                       onKeyDown={this.__handleKeyboardAdd.bind(this)} />

                <button className="truncate ob-button guided-setup-button guided-setup-button_neutral" 
                        title={labels.NewTerritoryButton}
                        onClick={this.__addNewPolicy.bind(this)}>{labels.NewTerritoryButton}</button>
            </div>
        );

    }


    __addNewPolicy() {

        // can not save while still saving
        if (this.saving) {
            return;
        }


        let name = this.policyNameInput.value;

        if (!name) {
            alert(labels.PolicyNameBlank);
            return;
        }

        if (!name.replace(/\s/g, '').length) {
            alert(labels.PolicyNameBlank);
            return;
        }

        this.saving = true;
        this.policyNameInput.value = '';

        this.props.onNewPolicy(name, () => {
            this.saving = false;
        })

    }

    __handleKeyboardAdd(e) {

        // if Enter is pressed
        if (e.which === 13) {
            this.__addNewPolicy();
        }
    }

}