import React, { Component } from 'react';
import labels from '../../../../utils/customLabels';
import './styles.css';

const DAY_INDEX = [ 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday' ];

export default class SlotsPreview extends Component {

    constructor(props) {
        super(props);
    }


    render() {

        if (!this.props.slots) {
            return null;
        }


        let days = this.props.slots.map( (slots, i) => {
            return this.__renderDay(DAY_INDEX[i], slots)
        });

        return (
            <div>
                <div id="SlotsViewContainer">
                    <div className="slots-prev-exp">{labels.ExplainPreviewSlots}</div>
                    {days}
                </div>

                <div id="TimezoneWarning">{labels.GuidedSetup_appt_booking_timezone_info}</div>
            </div>
        );
    }



    __renderDay(dayName, dailySlots) {

        let slotElements = dailySlots.map(SlotsPreview.formatTimeSlot);

        if (slotElements.length === 0) {
            slotElements = (<div className="single-day-slot">{labels.OnboardingNoSlots}</div>);
        }


        return (
            <div className="day-container-slots">

                <div className="day-name-container-slots">
                    {labels.Days[dayName]}
                </div>

                {slotElements}

            </div>
        );

    }


    static formatTimeSlot(slot) {

        let startMinutes = slot.startMinutes < 9 ? '0' + slot.startMinutes : slot.startMinutes,
            endMinutes = slot.endMinutes < 9 ? '0' + slot.endMinutes : slot.endMinutes;

        if (window.isAMPM) {

            let startHour = slot.startHour,
                endHour = slot.endHour;

            if (slot.startHour === 0) {
                startHour = '12' + ':' + startMinutes + 'AM';
            } else if (slot.startHour === 12) {
                startHour = '12' + ':' + startMinutes + 'PM';
            } else if (slot.startHour > 12) {
                startHour = (startHour - 12).toString() + ':' + startMinutes + 'PM';
            } else {
                startHour = startHour + ':' + startMinutes + 'AM';
            }

            if (slot.endHour === 0) {
                endHour = '12' + ':' + endMinutes + 'AM';
            } else if (slot.endHour === 12) {
                endHour = '12' + ':' + endMinutes + 'PM';
            } else if (slot.endHour > 12) {
                endHour = (endHour - 12).toString() + ':' + endMinutes + 'PM';
            } else {
                endHour = endHour + ':' + endMinutes + 'AM';
            }

            return (
                <div className="single-day-slot">
                    {startHour} - {endHour}
                </div>
            )

        }


        else {

            return (
                <div className="single-day-slot">
                    {slot.startHour}:{startMinutes} - {slot.endHour}:{endMinutes}
                </div>
            )

        }

    }


}

