import callRemoteAction from '../utils/callRemoteAction';

class WorkType {

    constructor() {
        this.synced = false;
    }

    get() {

        if (this.synced) {
            return new Promise( resolve => resolve(this.workTypes) );
        }


        return new Promise( (resolve, reject) => {

            callRemoteAction('getWorkTypes').then(workTypes => {
                this.workTypes = workTypes;
                this.synced = true;
                resolve(this.workTypes);
            }).catch(ex => {
                this.synced = false;
                console.error('Unable to get work types');
                reject(ex);
            });

        });
    }

}

let workType = new WorkType();

export default workType;