import React, { Component } from 'react';
import OperatingHoursSelect from '../../../OperatingHoursSelect/OperatingHoursSelect';
import './styles.css';
import labels from '../../../../utils/customLabels';
import callRemoteAction from '../../../../utils/callRemoteAction';
import Lightbox from '../../../Lightbox/Lightbox';
import OperatingHoursCreator from '../../../OperatingHoursCreator/OperatingHoursCreator';


class BulkTerritoriesActions extends Component {

    constructor(props) {
        super(props);

        this.state = {
            selectedOperatingHours: BulkTerritoriesActions.__mutualTerritoriesHours(props.territories),
            territories: props.territories,
            showWorkingHoursCreator: false,
            addNewOh: null
        }
    }

    render() {

        let externalDiv = null;

        if (this.state.selectedOperatingHours !== 'none') {
            externalDiv = (<div>
                <a className="open-external-link" href={'../' + this.state.selectedOperatingHours} target="_blank">{labels.OpenInLayout}</a>
            </div>);
        }

        return (
            <div className="bulk-ter-actions">
                <h2>{labels.XSelectedTerritories.replace('$0',this.props.territories.length)}</h2>

                <OperatingHoursSelect multiselect={true} selected={this.state.selectedOperatingHours} onChange={this.__operationHoursChanged.bind(this)} addNewOh={this.state.addNewOh} />


                <div className="create-operating-hours">

                    {externalDiv}

                    <div onClick={this.__openWorkingHoursCreator.bind(this)}>
                        {labels.CreateNewOH}
                    </div>
                </div>

                <Lightbox show={this.state.showWorkingHoursCreator} height="420px" width="650px" handleVisibility={this.__closeWorkingHoursCreator.bind(this)}>
                    <OperatingHoursCreator afterSave={this.__afterSaveOperatingHours.bind(this)} />
                </Lightbox>

            </div>
        );

    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            selectedOperatingHours: BulkTerritoriesActions.__mutualTerritoriesHours(nextProps.territories),
            territories: nextProps.territories,
            showWorkingHoursCreator: this.state.showWorkingHoursCreator,
            addNewOh: this.state.addNewOh
        });
    }

    __operationHoursChanged(ohId) {

        if (ohId === 'none') {
            return;
        }

        let territoryToSave = this.state.territories.map(t => {
            return {
                Id: t.Id,
                OperatingHoursId: ohId
            }
        });

        callRemoteAction('updateServiceTerritories', JSON.stringify(territoryToSave))
            .then(territories => {
                this.props.afterSave(territories);
            })
            .catch(ex => {
                console.error(ex);
            })



    }


    __openWorkingHoursCreator() {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: true} ));
    }

    __closeWorkingHoursCreator() {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: false} ));
    }

    __afterSaveOperatingHours(oh) {
        this.setState(Object.assign({}, this.state, {showWorkingHoursCreator: false, addNewOh: oh } ));
    }


    static __mutualTerritoriesHours(territories) {

        let oh = territories[0].OperatingHoursId;

        for (let i=1; i<territories.length; i++) {
            if (oh !== territories[i].OperatingHoursId) {
                return 'none';
            }
        }

        return oh;

    }
}





export default BulkTerritoriesActions;