import React, { Component } from 'react';
import { Route } from 'react-router-dom';

import DefineTerritories from './DefineTerritories/DefineTerritories';
import WorkTypesAndSkills from './WorkTypesAndSkills/WorkTypesAndSkills';
import AppointmentBooking from './AppointmentBooking/AppointmentBooking';
import ResourcesDefenition from './ResourcesDefenition/ResourcesDefenition';
import SchedulingPolicies from './SchedulingPolicies/SchedulingPolicies';
import SharingUsersTerritories from './SharingUsersTerritories/SharingUsersTerritories';
import Home from './Home/Home';
import Toast from '../Toast/Toast';
import callRemoteAction from '../../utils/callRemoteAction';
import labels from '../../utils/customLabels';

import './styles.css';

class WorkCanvas extends Component {

    constructor() {
        super();

        this.state = { saving: false };

        callRemoteAction.setComponenetSavingStatus = (saving) => {
            Toast.toastElement.style.display = saving ? 'inline-block' : 'none';
        };
    }

    render() {
        return (
            <div className="workCanvasContainer">
                <Toast text={labels.OnboardingSaving} />
                <Route path='/' exact component={Home} />
                <Route path='/DefineTerritories' component={DefineTerritories} />
                <Route path='/WorkTypesAndSkills' component={WorkTypesAndSkills} />
                <Route path='/AppointmentBooking' component={AppointmentBooking} />
                <Route path='/ResourcesDefenition' component={ResourcesDefenition} />
                <Route path='/SchedulingPolicies' component={SchedulingPolicies} />
                <Route path='/SharingUsersTerritories' component={SharingUsersTerritories} />
            </div>
        );
    }
}

export default WorkCanvas;
