import React, { Component } from 'react';
import './styles.css';
import labels from '../../../../utils/customLabels';
import lsdIcons from '../../../../utils/lsdIcons';


class PoliciesList extends Component {

    constructor(props) {
        super(props);
    }


    render() {


        let list =  [];
        this.props.policies.forEach(policy => {

            let cssClass = this.props.selectedId === policy.Id ? "policy-li policy-selected": "policy-li";

            list.push(
                <div className={cssClass} onClick={this.__policySelected.bind(this, policy.Id)} >
                    <h1 className="truncate">
                        {policy.Name}
                    </h1>
                    <svg onClick={this.__clonePolicy.bind(this, policy.Id)}
                             aria-hidden="true"
                             className="slds-icon icon-on-header" >
                                <use href={lsdIcons.clone}></use>
                    </svg>
                </div>
            );

        });

        return (
            <div id="PoliciesList">
                {list}
            </div>
        );

    }

    __policySelected(id) {
        this.props.onSelected && this.props.onSelected(id);
    }

    __clonePolicy(id, event) {
        event.stopPropagation();

        if (window.confirm(labels.ConfirmPolicyClone)) {
            this.props.onClone && this.props.onClone(id);
        }
    }

}


export default PoliciesList;