import DhtmlxTreeNode from './DhmlxTreeNode';


export function treeifyTerritories(nodes) {

    let dhtmlxNodes = nodes.map(m => new DhtmlxTreeNode(m)),
        tree = [],
        mappedArr = {};

    dhtmlxNodes.forEach( territory => {
        mappedArr[territory.id] = territory;
        mappedArr[territory.id]['items'] = [];
    });

    for (let id in mappedArr) {

        if (mappedArr.hasOwnProperty(id)) {

            let mappedElem = mappedArr[id];
            mappedElem.text = encodeHTML(mappedElem.text);

            // If the element is not at the root level, add it to its parent array of items.
            if (mappedElem.parent) {
                mappedArr[mappedElem['parent']]['items'].push(mappedElem);
            }

            // If the element is at the root level, add it to first level elements array.
            else {
                tree.push(mappedElem);
            }
        }
    }

    return tree.sort( (a,b) => {
        if (a.text.toLowerCase() > b.text.toLowerCase()) return 1;
        if (a.text.toLowerCase() < b.text.toLowerCase()) return -1;
        return 0;
    });

}


export function nodeToSobject(node) {
    return {
        ParentTerritoryId: node.pId || null,
        Id: node.id,
        Name: node.text,
        operatingHoursId: node.OperatingHours && node.OperatingHours.Id
    }
}

export function decodeHTML(input) {
    let domparser = new DOMParser().parseFromString(input, "text/html");
    return domparser.documentElement.textContent;
}

function encodeHTML(s) {
    return s.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/"/g, '&quot;');
}

function isInConsole() {

    if (typeof window.sforce !== "undefined") {
        return window.sforce.console.isInConsole()
    }

    return false;
}


