export function openSObjectLink(objectId) {

    let isConsole = openConsoleTab(null, objectId);

    if (!isConsole) {
        window.open('../' + objectId, '_blank');
    }
}

// open url in console (if not in console, do nothing)
function openConsoleTab(e, id) {


    let isInConsole = (typeof window.sforce !== "undefined") ? window.sforce.console.isInConsole() : null;

    if (isInConsole) {

        if (e)
            e.preventDefault();

        window.sforce.console.generateConsoleUrl(['/' + id], function (result) {
            if (result.success)
                window.sforce.console.openConsoleUrl(null, result.consoleUrl, true);
            else
                openLightningPrimaryTab(id);
        });

        return true;
    }

    return false;
}


function openLightningPrimaryTab(id) {
    window.sforce.console.openPrimaryTab(null, '/'+id, true);
}