import React, { Component } from 'react';
import NewTerritory from './NewTerritory/NewTerritory';
import callRemoteAction from '../../../../utils/callRemoteAction';
import { treeifyTerritories, nodeToSobject } from '../../../../utils/Utils';
import labels from '../../../../utils/customLabels';
import './styles.css';


const NO_SALESFORCE_ID_YET = 'NOT_SOBJECT';

class TerritoriesTree extends Component {

    constructor() {
        super();
    }


    render() {
        return (
            <div className="tree-container">
                <NewTerritory onNewAdded={this.__addNewTerritory.bind(this)}
                              validate={this.__validateNewTerritory.bind(this)} />

                <div id="dhmlxTerritoriesTree"></div>
            </div>

        );
    }

    encodeHTML(s) {
        return s.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/"/g, '&quot;');
    }


    componentWillReceiveProps(nextProps) {

        // got new tree
        if (!this.props.territories && nextProps.territories) {

            this.territories = nextProps.territories;

            this.tree = new window.dhtmlXTreeView({
                parent: "dhmlxTerritoriesTree",
                dnd: true,
                multiselect: true,
                context_menu: true,
                iconset: "font_awesome",
                items: treeifyTerritories(nextProps.territories)
            });

            this.__attachTreeEvents(this.tree);
        }


        // check if an updated territory was just saved
        let territory = nextProps.updatedTerritory && nextProps.updatedTerritory[0];

        if (territory) {
            this.tree.setItemText(territory.Id, this.encodeHTML(territory.Name));
            this.tree.items[territory.Id].pId = territory.ParentTerritoryId;
            this.tree.items[territory.Id].userdata = territory;
            //this.tree.refreshItem(territory.Id);
        }

    }


    __validateNewTerritory(name) {

        if (!name.replace(/\s/g, '').length) {
            alert(labels.TerritoryNameCantBeBlank);
            return false;
        }


        // check if name already exist
        for (let i=0; i<this.territories.length; i++) {
            if (this.territories[i].Name.toUpperCase() === name.toUpperCase()) {
                alert(labels.TerritoryNameExist);
                return false;
            }
        }

        return true;
    }


    __attachTreeEvents(tree) {

        let me = this;

        // change the territories tree and save to Salesforce
        tree.attachEvent("onDrop", function (id, parentId) {

            let changedTerritories = [nodeToSobject(tree.items[id])];

            callRemoteAction('updateServiceTerritories', JSON.stringify(changedTerritories))
                .then(territories => {
                    me.props.afterSave(territories);
                })
                .catch(ex => {
                    console.error(ex);
                })

        });


        // select a territory to display its form
        tree.attachEvent("onClick", id => {
            this.props.selectTerritoryHandler(tree.items[id], this.tree);
            return true;
        });

        // don't allow dragging if not saved
        this.tree.attachEvent("onBeforeDrag", id => id.indexOf(NO_SALESFORCE_ID_YET) === -1);

    }


    __addNewTerritory(territory) {

        this.tree.addItem(territory.Id, this.encodeHTML(territory.Name));
        this.tree.selectItem(territory.Id);

        this.tree.items[territory.Id].userdata = territory;
        this.props.selectTerritoryHandler(this.tree.items[territory.Id], this.tree);

        this.territories.push(territory);
        this.props.afterSave([territory]);

    }
}

export default TerritoriesTree;
