import React, { Component } from 'react';
import callRemoteAction from '../../../../utils/callRemoteAction';
import labels from '../../../../utils/customLabels';
import '../../../../globalStyles.css';
import debounce from 'lodash/debounce';
import SkillsSelector from './SkillsSelector/SkillsSelector';
import Tooltip from "../../../Tooltip/Tooltip";
import ObjectNames from "../../../../utils/ObjectNames";
import './styles.css';
import { openSObjectLink } from '../../../../utils/Utils';


class WorkTypeForm extends Component {

    constructor() {

        super();

        this.tooltipTextDueDate = (<span>
            {labels.DueDateOffsetTooltip}
        </span>);

        this.tooltipTextName = (<span>
            {labels.worktype_name_tooltip}
        </span>);

        this.tooltipTextAutoCreate = (<span>
            {labels.worktype_auto_create_tooltip}
        </span>);

        this.tooltipTextSkillReq = (<span>
            {labels.worktype_skill_req_tooltip}
        </span>);



        this.state = {
            workType: {
                Id: null,
                Name: null,
                Description: null,
                [ObjectNames.WorkType.Due_Date_Offset__c]: null,
                EstimatedDuration: null,
                DurationType: null,
                ShouldAutoCreateSvcAppt: null,
                SkillRequirements: null
            },
        };

        this.__saveChanges = debounce(this.__saveChanges, 2000);

    }

    componentWillMount() {
        if (this.props && this.props.workType) {
            this.nameBeforeSave = this.props.workType.Name;
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.workType) {
            this.nameBeforeSave = nextProps.workType.Name;

            let w = {
                    Id: nextProps.workType.Id || '',
                    Name: nextProps.workType.Name || '',
                    Description: nextProps.workType.Description || '',
                    [ObjectNames.WorkType.Due_Date_Offset__c]: nextProps.workType[ObjectNames.WorkType.Due_Date_Offset__c],
                    EstimatedDuration: nextProps.workType.EstimatedDuration || '',
                    DurationType: nextProps.workType.DurationType || '',
                    ShouldAutoCreateSvcAppt: nextProps.workType.ShouldAutoCreateSvcAppt,
                    SkillRequirements: nextProps.workType.SkillRequirements
                };

            if (w[ObjectNames.WorkType.Due_Date_Offset__c] == undefined)
                w[ObjectNames.WorkType.Due_Date_Offset__c] = '';

            this.setState(Object.assign({}, this.state, { workType: w }));
        }
    }

    render() {

        if (!this.props.workType) {
            return (
                <div className="side-form">
                    <div className="select-a">
                        {labels.NoWorkTypeSelectedText}
                    </div>
                </div>
            )
        }

        return (

            <div className="side-form form-arrow">

                <div className="form-header">
                    <h2 className="truncate">{this.nameBeforeSave || this.state.workType.Name}</h2>
                    <span className="truncate" onClick={this.__openSObjectLink.bind(this, this.state.workType.Id)}>{labels.Open_WorkType_Record}</span>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.OpHoursName}<Tooltip text={this.tooltipTextName} /></div>
                    <div className="form-input">
                        <input type="text" maxlength="100" value={this.state.workType.Name}
                               ref={ input => { this.wt_Name = input }}
                               onChange={this.__wtFieldChanged.bind(this, 'Name')}/>
                    </div>
                </div>

                <div className="form-row-container">
                    <div className="form-label">{labels.PolicyDescription}</div>
                    <div className="form-input">
                        <textarea
                            value={this.state.workType.Description}
                            ref={ input => { this.wt_Description = input }}
                            onChange={this.__wtFieldChanged.bind(this, 'Description')} />
                    </div>
                </div>
                <div className="form-row-container" style={{marginBottom: 35+'px'}}>
                    <div className="form-label">{labels.DueDateOffset}<Tooltip text={this.tooltipTextDueDate} /></div>
                    <div className="form-input">
                        <input type="number" min="0" value={this.state.workType[ObjectNames.WorkType.Due_Date_Offset__c]}
                               ref={ input => { this['wt_' + ObjectNames.WorkType.Due_Date_Offset__c] = input }}
                               onChange={this.__wtFieldChanged.bind(this, ObjectNames.WorkType.Due_Date_Offset__c)}/>
                        <div className="due-date-calculated">{this.__calcDueDateInDays(this.state.workType[ObjectNames.WorkType.Due_Date_Offset__c])}</div>

                    </div>
                </div>
                <div className="form-row-container">
                    <div className="form-label">{labels.EstimatedDuration}</div>
                    <div className="form-input">
                        <input type="number" min="0" value={this.state.workType.EstimatedDuration}
                               ref={ input => { this.wt_EstimatedDuration = input }}
                               onChange={this.__wtFieldChanged.bind(this, 'EstimatedDuration')}/>
                    </div>
                </div>
                <div className="form-row-container">
                    <div className="form-label">{labels.Duration_Type}</div>
                    <div className="form-input form-select">
                        <select value={this.state.workType.DurationType}
                                ref={ input => { this.wt_DurationType = input }}
                                onChange={this.__wtFieldChanged.bind(this, 'DurationType')} >
                            <option value="Minutes">{labels.Minutes}</option>
                            <option value="Hours">{labels.Hours}</option>
                        </select>
                    </div>
                </div>
                <div className="form-row-container">
                    <div className="form-label">{labels.AutoCreateServiceAppointment}<Tooltip text={this.tooltipTextAutoCreate} /></div>
                    <div className="form-input checkbox_toggle">
                        <input type="checkbox" checked={this.state.workType.ShouldAutoCreateSvcAppt}
                               ref={ input => { this.wt_ShouldAutoCreateSvcAppt = input }}
                               onChange={this.__wtCheckboxFieldChanged.bind(this, 'ShouldAutoCreateSvcAppt')}/>
                    </div>
                </div>
                <div className="form-row-container">
                    <div className="form-label">{labels.Skill_Requirements}<Tooltip text={this.tooltipTextSkillReq} tipPosition='up'/></div>
                    <SkillsSelector skills={this.props.skills}
                                    requiredSkills={this.state.workType.SkillRequirements}
                                    addRequiredSkill={this.__addRequiredSkill.bind(this)}
                                    removeRequiredSkill={this.__removeRequiredSkill.bind(this)}
                    />
                </div>
            </div>
        );
    }

    __wtFieldChanged(fieldName) {
        let workType = Object.assign({}, this.state.workType);
        workType[fieldName] = this['wt_' + fieldName].value;

        if (fieldName == ObjectNames.WorkType.Due_Date_Offset__c && (this['wt_' + fieldName].value === '')) {
            workType[fieldName] = null;
        }

        this.setState( Object.assign({}, this.state, {workType} ) );

        this.__saveChanges();
    }

    __wtCheckboxFieldChanged(fieldName) {
        let workType = Object.assign({}, this.state.workType);
        workType[fieldName] = this['wt_' + fieldName].checked;

        this.setState( Object.assign({}, this.state, {workType} ) );

        this.__saveChanges();
    }

    __addRequiredSkill(skillId) {
        if (!skillId)
            return;

        callRemoteAction('addRequiredSkill', this.state.workType.Id, skillId)
            .then(wt => {
                this.props.afterSave(wt);
            })
            .catch(ex => {
                console.error(ex);
            })
    }

    __removeRequiredSkill(reqSkill) {

        if (!reqSkill)
            return;

        callRemoteAction('removeRequiredSkill', this.state.workType.Id, reqSkill.Id)
            .then(wt => {
                this.props.afterSave(wt);
            })
            .catch(ex => {
                console.error(ex);
            })
    }

    __calcDueDateInDays(minutes) {
        if (minutes >= 1440)
            return Math.floor(minutes/60/24) + ' ' + labels.DaysLabel;
        if (minutes >= 60)
            return ((minutes/60 % 10 == 0) ? minutes/60 : (minutes/60).toFixed(2)) + ' ' + labels.Hours;

        return '';

    }

    __saveChanges() {

        if (!this.state.workType.Name) {
            return;
        }

        // check if name already exist
        for (let i=0; i<this.props.workTypes.length; i++) {
            if (this.props.workTypes[i].Name === this.state.workType.Name && this.props.workTypes[i].Id !== this.state.workType.Id) {
                return;
            }
        }


        console.info('saving.....');

        let workTypeToSave = Object.assign({}, this.state.workType);
        let requiredSkills = Object.assign({}, workTypeToSave.SkillRequirements);
        delete workTypeToSave.SkillRequirements;

        //console.log(workTypeToSave);
        //console.log(JSON.stringify([workTypeToSave]));

        callRemoteAction('updateWorkType', JSON.stringify(workTypeToSave), JSON.stringify([requiredSkills]))
            .then(wt => {
                this.props.afterSave(wt);
            })
            .catch(ex => {
                console.error(ex);
            })

    }

    __openSObjectLink(id, ev) {
        ev.preventDefault();
        openSObjectLink(id);
    }

}

export default WorkTypeForm;