class DhtmlxTreeNode {

    constructor(sobject) {

        // dhtmlx stuff
        this.items = [];
        this.open = false;

        // stuff from Salesforce
        this.parent = sobject.ParentTerritoryId;
        this.address = sobject.Address;
        this.id = sobject.Id;
        this.text = sobject.Name;
        this.operatingHours = sobject.OperatingHours;
        this.userdata = sobject;

    }
}

export default DhtmlxTreeNode;