import React, { Component } from 'react';
import debounce from 'lodash/debounce';
import './styles.css';

export default class InlineResourceFieldEdit extends Component {

    constructor(props) {

        super(props);

        this.state = {
            value: this.props.value,
            oldValue: this.props.value,
        };

        this.saveValueDebounced = debounce(this.saveValueDebounced, 5).bind(this);
    }


    render() {

        if (this.props.type === 'IsActive') {

            return (
                <input type="checkbox"
                       onChange={this.valueChanged.bind(this)}
                       checked={this.state.value} />
            )

        }


        return (<input className="rd-inline-edit"
                       onChange={this.valueChanged.bind(this)}
                       onBlur={this.checkIfEmpty.bind(this)}
                       type="text"
                       value={this.state.value} />)
    }


    componentWillReceiveProps(nextProps) {

        if (this.props.value === nextProps.value) {
            this.setState({value: nextProps.value});
        }

        this.setState({value: nextProps.value});

    }


    valueChanged(e) {

        if (this.props.type === 'IsActive') {
            this.setState({value: e.target.checked});
            this.saveValueDebounced(e.target.checked);
            return;
        }

        this.setState({value: e.target.value, oldValue: this.state.value});

        this.saveValueDebounced(e.target.value);
    }

    saveValueDebounced(value) {

        // do not save if blank
        if (this.props.type === 'resourceName' && value === '') {
            return;
        }

        this.props.onSave(value, this.props.type, this.props.resourceId);

    }

    checkIfEmpty() {

        if (this.props.type === 'resourceName' && this.state.value === '') {
            this.setState({value: this.state.oldValue});
        }

    }

}

